import { Component, Input, OnInit } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { IEmployeeMilitaryModel } from "app/personnel-setup/models/employee-personal-info";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmployeeMilitaryDataService } from "app/personnel-setup/services/employees/employee-military-data.service";
import { EmployeeMilitaryDataDialogComponent } from "../emp-military-data-dialog/emp-military-data-dialog.component";

@Component({
	selector: "emp-military-data-card",
	templateUrl: "./emp-military-data-card.component.html",
	styleUrls: ["./emp-military-data-card.component.scss"],
})
export class EmployeeMilitaryDataCardComponent extends AppHelpers implements OnInit {
	@Input() employeeId: number;
	employeeMilitaryInfo: IEmployeeMilitaryModel;

	isLoaded: boolean = false;
	showTwoDates: boolean = false;

	constructor(
		private employeeMilitaryDataService: EmployeeMilitaryDataService,
		private dialog: MatDialog
	) {
		super();
	}

	ngOnInit(): void {}

	override onChangeBusinessAndCompanyId(): void {
		this.employeeMilitaryInfo = null;
		if (this.employeeId > 0 && this.checkNoBusinessOrCompany()) this.getAllByEmployeeId();
		else this.isLoaded = true;
	}

	getAllByEmployeeId() {
		this.employeeMilitaryInfo = null;
		this.employeeMilitaryDataService
			.getAllByEmployeeId(this.employeeId)
			.subscribe((res: IResponse<IEmployeeMilitaryModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.employeeMilitaryInfo = res.data[0];
					this.isLoaded = true;
				} else this.errorList(res.message.messages);
			});
	}

	openAddEditDialog(): void {
		this.dialog
			.open(EmployeeMilitaryDataDialogComponent, {
				data: {
					employeeId: this.employeeId,
				},
				position: {
					top: "40px",
				},
				minWidth: "600px",
				panelClass: "dialog-emp-form",
				autoFocus: false,
			})
			.afterClosed()
			.subscribe((updated: boolean) => {
				if (updated) this.getAllByEmployeeId();
			});
	}
}
