import { Component, Input, OnInit } from "@angular/core";
import { EmpLocationDialogComponent } from "../emp-location-dialog/emp-location-dialog.component";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { IEmpLocationModel } from "app/personnel-setup/models/employee-employment-info";
import { EmployeeLocationService } from "app/personnel-setup/services/employees/employee-location.service";
import { MatDialog } from "@angular/material/dialog";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";

@Component({
  selector: "app-emp-location-card",
  templateUrl: "./emp-location-card.component.html",
  styleUrls: ["./emp-location-card.component.scss"],
})
export class EmpLocationCardComponent extends AppHelpers implements OnInit {
  @Input() employeeId: number;
  employeeLocationInfo: IEmpLocationModel[];

  isLoaded: boolean = false;

  constructor(
    private employeeLocationService: EmployeeLocationService,
    private dialog: MatDialog
  ) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.employeeLocationInfo = null;
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany())
      this.getAllByEmployeeId();
    else this.isLoaded = true;
  }

  getAllByEmployeeId() {
    this.employeeLocationInfo = null;
    this.employeeLocationService
      .getAllByEmployeeId(this.employeeId)
      .subscribe((res: IResponse<IEmpLocationModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.employeeLocationInfo = res.data;
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialog
      .open(EmpLocationDialogComponent, {
        data: {
          employeeId: this.employeeId,
        },
        position: {
          top: "40px",
        },
        minWidth: "600px",
        panelClass: "dialog-emp-form",
        autoFocus: false,
      })
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) this.getAllByEmployeeId();
      });
  }
}
