import { Component, OnInit, Inject } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import { ICountryModel } from "app/hr-setup/models/country";
import { IGovernorateModel } from "app/hr-setup/models/governorate";
import { GovernorateService } from "app/hr-setup/services/governorate.service";
import {
  IEmpLocationModel,
  IEmpLocationRequest,
} from "app/personnel-setup/models/employee-employment-info";
import { EmployeeLocationService } from "app/personnel-setup/services/employees/employee-location.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
  selector: "app-emp-location-dialog",
  templateUrl: "./emp-location-dialog.component.html",
  styleUrls: ["./emp-location-dialog.component.scss"],
})
export class EmpLocationDialogComponent extends AppHelpers implements OnInit {
  employeeId: number = 0;
  updated: boolean = false;

  mandatoryColomns: string[] = [
    "Ser",
    // "id",
    "country",
    "governorate",
    "address",
    "froM_DATE",
    "tO_DATE",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmpLocationModel>;

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;

  governorateList: IGovernorateModel[];

  constructor(
    private employeeLocationService: EmployeeLocationService,
    private governorateService: GovernorateService,
    @Inject(MAT_DIALOG_DATA) public data: { employeeId: number },
    public dialogRef: MatDialogRef<EmpLocationDialogComponent>
  ) {
    super();
    this.employeeId = data.employeeId;
  }

  ngOnInit(): void {
    this.form = this.fb.group(
      {
        id: [0, { nonNullable: true }],
        countryId: [null, [Validators.required]],
        Governorate_ID: [
          { value: null, disabled: true },
          [Validators.required],
        ],
        address: [null, [Validators.required]],
        froM_DATE: [null],
        tO_DATE: [null],
      },
      {
        validator: CustomValidator.compareDates("froM_DATE", "tO_DATE"),
      }
    );

    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  onCloseDialog() {
    this.dialogRef.close(this.updated);
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany()) {
      this.getAll();
    }
  }

  getAll() {
    this.employeeLocationService
      .getAllByEmployeeId(this.employeeId)
      .subscribe((res: IResponse<IEmpLocationModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmpLocationRequest = {
        Person_ID: this.employeeId,
        id: this.form.value.id,
        countryId: this.form.value.countryId,
        Governorate_ID: this.form.value.Governorate_ID,
        address: this.form.value.address,
        froM_DATE: this.form.value.froM_DATE,
        tO_DATE: this.form.value.tO_DATE,
      };

      model.id == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmpLocationRequest) {
    this.showLoading();
    this.employeeLocationService
      .create(model)
      .subscribe((res: IResponse<IEmpLocationModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IEmpLocationRequest) {
    this.showLoading();
    this.employeeLocationService
      .update(model)
      .subscribe((res: IResponse<IEmpLocationModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  employeeLocationToEdit(employeeLocationInfo: IEmpLocationModel) {
    this.form.patchValue(employeeLocationInfo);
    // console.log(this.form.value);
  }

  onDelete(employeeLocationInfo: IEmpLocationModel): void {
    let msg = this.msgTranslate(
      employeeLocationInfo.country,
      employeeLocationInfo.country
    );
    this.deleateDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(employeeLocationInfo);
    });
  }

  delete(employeeLocationInfo: IEmpLocationModel) {
    this.showLoading();
    this.employeeLocationService
      .Delete(employeeLocationInfo.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          if (employeeLocationInfo.id == this.formCtrls.id.value)
            this.clearForm();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }

  getAllGovernorateByCountryId(values: ICountryModel[]) {
    this.formCtrls.Governorate_ID.setValue(null);
    let countryId: number = values.length > 0 ? values[0].id : 0;
    if (countryId > 0) {
      this.showLoading();
      this.governorateService
        .getAllByCountryId(countryId)
        .subscribe((res: IResponse<IGovernorateModel[]>) => {
          if ((res.message.messageType = MessageTypes.Success)) {
            this.governorateList = res.data;
          } else this.errorList(res.message.messages);
          this.hideLoading();
        });
      this.formCtrls.Governorate_ID.enable();
    } else this.formCtrls.Governorate_ID.disable();
  }
}
