import { Component, OnInit, Inject } from "@angular/core";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "emp-job-info-dialog",
  templateUrl: "./emp-job-info-dialog.component.html",
  styleUrls: ["./emp-job-info-dialog.component.scss"],
})
export class EmpJobInfoDialogComponent extends AppHelpers implements OnInit {
  employeeId: number = 0;
  updated: boolean = false;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: { employeeId: number },
    public dialogRef: MatDialogRef<EmpJobInfoDialogComponent>
  ) {
    super();
    this.employeeId = data.employeeId;
  }

  ngOnInit(): void {
    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

  onCloseDialog() {
    this.dialogRef.close(this.updated);
  }

  changUpdated(value: boolean) {
    this.updated = value;
  }
}
