import { MatDialog } from "@angular/material/dialog";
import { Component, Input, OnInit } from "@angular/core";
import { IEmpMedicalInsuranceModel } from "app/personnel-setup/models/employee-employment-info";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { EmpMedicalInsuranceDialogComponent } from "../emp-medical-insurance-dialog/emp-medical-insurance-dialog.component";
import { EmployeeMedicalInsuranceService } from "app/personnel-setup/services/employees/employee-medical-insurance.service";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-emp-medical-insurance-card",
  templateUrl: "./emp-medical-insurance-card.component.html",
  styleUrls: ["./emp-medical-insurance-card.component.scss"],
})
export class EmpMedicalInsuranceCardComponent
  extends AppHelpers
  implements OnInit
{
  @Input() employeeId: number;
  employeeMedicalInsuranceInfo: IEmpMedicalInsuranceModel;

  isLoaded: boolean = false;

  constructor(
    private employeeMedicalInsuranceService: EmployeeMedicalInsuranceService,
    private dialog: MatDialog
  ) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.employeeMedicalInsuranceInfo = null;
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany())
      this.getAllByEmployeeId();
    else this.isLoaded = true;
  }

  getAllByEmployeeId() {
    this.employeeMedicalInsuranceInfo = null;
    this.employeeMedicalInsuranceService
      .getAllByEmployeeId(this.employeeId)
      .subscribe((res: IResponse<IEmpMedicalInsuranceModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.employeeMedicalInsuranceInfo = res.data[0];
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialog
      .open(EmpMedicalInsuranceDialogComponent, {
        data: {
          employeeId: this.employeeId,
        },
        position: {
          top: "40px",
        },
        minWidth: "600px",
        panelClass: "dialog-emp-form",
        autoFocus: false,
      })
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) this.getAllByEmployeeId();
      });
  }
}
