import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IEmployeeAddressModel,
  IEmployeeAddressRequest,
} from "../../models/employee-personal-info";

@Injectable({
  providedIn: "root",
})
export class EmployeeAddressService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmployeeAddressRequest) {
    return this.post<IResponse<IEmployeeAddressModel>>(
      "Personnel/Employees/Create_address",
      data
    );
  }

  update(data: IEmployeeAddressRequest) {
    return this.post<IResponse<IEmployeeAddressModel>>(
      "Personnel/Employees/Edit_address",
      data
    );
  }

  getAllByEmployeeId(employeeId: number) {
    return this.get<IResponse<IEmployeeAddressModel[]>>(
      "Personnel/Employees/GetAll_address",
      {
        params: {
          $filter: `id eq ${employeeId}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Personnel/Employees/Delete_address", {
      params: { Id: Id },
    });
  }
}
