import { MatDialog } from "@angular/material/dialog";
import { Component, Input, OnInit } from "@angular/core";
import { EmpCustodiesDialogComponent } from "../emp-custodies-dialog/emp-custodies-dialog.component";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { IEmpCustodyModel } from "app/personnel-setup/models/employee-employment-info";
import { EmployeeCustodiesService } from "app/personnel-setup/services/employees/employee-custodies.service";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";

@Component({
  selector: "app-emp-custodies-card",
  templateUrl: "./emp-custodies-card.component.html",
  styleUrls: ["./emp-custodies-card.component.scss"],
})
export class EmpCustodiesCardComponent extends AppHelpers implements OnInit {
  @Input() employeeId: number;
  employeeCustodyInfo: IEmpCustodyModel[];

  isLoaded: boolean = false;

  constructor(
    private employeeCustodiesService: EmployeeCustodiesService,
    private dialog: MatDialog
  ) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.employeeCustodyInfo = null;
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany())
      this.getAllByEmployeeId();
    else this.isLoaded = true;
  }

  getAllByEmployeeId() {
    this.employeeCustodyInfo = null;
    this.employeeCustodiesService
      .getAllByEmployeeId(this.employeeId)
      .subscribe((res: IResponse<IEmpCustodyModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.employeeCustodyInfo = res.data;
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialog
      .open(EmpCustodiesDialogComponent, {
        data: {
          employeeId: this.employeeId,
        },
        position: {
          top: "40px",
        },
        minWidth: "600px",
        panelClass: "dialog-emp-form",
        autoFocus: false,
      })
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) this.getAllByEmployeeId();
      });
  }
}
