import { Injectable } from "@angular/core";
import {
  IEmployeeBankingAccountModel,
  IEmployeeBankingAccountRequest,
} from "app/personnel-setup/models/payments-information";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeBankingAccountService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmployeeBankingAccountRequest) {
    return this.post<IResponse<IEmployeeBankingAccountModel>>(
      "Personnel/Employees/Create_address",
      data
    );
  }

  update(data: IEmployeeBankingAccountRequest) {
    return this.post<IResponse<IEmployeeBankingAccountModel>>(
      "Personnel/Employees/Edit_address",
      data
    );
  }

  getAllByEmployeeId(employeeId: number) {
    return this.get<IResponse<IEmployeeBankingAccountModel[]>>(
      "Personnel/Employees/GetAll_address",
      {
        params: {
          $filter: `id eq ${employeeId}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Personnel/Employees/Delete_address", {
      params: { Id: Id },
    });
  }
}
