import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import {
	IEmployeeContactModel,
	IEmployeeContactRequest,
} from "app/personnel-setup/models/employee-personal-info";
import { EmployeeContactService } from "app/personnel-setup/services/employees/employee-contact.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { Idropdown } from "app/shared/interfaces/dropdown.interface";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ContactService } from "app/hr-setup/services/contact.service";

@Component({
	selector: "app-employee-contact-dialog",
	templateUrl: "./emp-contact-dialog.component.html",
	styleUrls: ["./emp-contact-dialog.component.scss"],
})
export class EmployeeContactDialogComponent extends AppHelpers implements OnInit {
	@Input() employeeId: number = 0;
	@Output() changUpdated = new EventEmitter<boolean>();

	mandatoryColomns: string[] = [
		"Ser",
		// "id",
		"contactType",
		"contact",
		"contactCategory",
		"primary",
		"actions",
	];
	optionalColomns: string[] = [];
	dataSource: MatTableDataSource<IEmployeeContactModel>;

	isLoaded: boolean = false;

	form: FormGroup;
	submitted: boolean = false;

	contactTypesList: Idropdown[];
	contactCategoryList: Idropdown[];

	constructor(
		private employeeContactService: EmployeeContactService,
		private contactService: ContactService
	) {
		super();
	}

	ngOnInit(): void {
		this.form = this.fb.group({
			id: [0, { nonNullable: true }],
			CONTACT_TYPE_ID: [null, [Validators.required]],
			CONTACT_Category_ID: [null],
			Value: [null, [Validators.required]],
			IsPrimary: [null],
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.checkNoBusinessOrCompany()) {
			this.getAllContactTypes();
			if (this.employeeId > 0) this.getAll();
		}
	}

	getAll() {
		this.employeeContactService
			.getAllByEmployeeId(this.employeeId)
			.subscribe((res: IResponse<IEmployeeContactModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.dataSource = new MatTableDataSource(res.data);
					this.isLoaded = true;
				} else this.errorList(res.message.messages);
			});
	}

	submit() {
		// console.log(this.form.value);
		this.submitted = true;
		if (this.form.valid) {
			const model: IEmployeeContactRequest = {
				Person_ID: this.employeeId,
				id: this.form.value.id,
				CONTACT_TYPE_ID: this.form.value.CONTACT_TYPE_ID,
				CONTACT_Category_ID: this.form.value.CONTACT_Category_ID,
				Value: this.form.value.Value,
				IsPrimary: this.form.value.IsPrimary ?? false,
			};

			model.id == 0 ? this.create(model) : this.update(model);
		} // else this.toastr.error("General.formNotValid");
	}

	create(model: IEmployeeContactRequest) {
		this.showLoading();
		this.employeeContactService.create(model).subscribe((res: IResponse<IEmployeeContactModel>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.clearForm();
				this.addSuccessfullyMsg(res?.message?.messageTypeName);
				this.getAll();
				this.changUpdated.emit(true);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	update(model: IEmployeeContactRequest) {
		this.showLoading();
		this.employeeContactService.update(model).subscribe((res: IResponse<IEmployeeContactModel>) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.clearForm();
				this.updateSuccessfullyMsg(res?.message?.messageTypeName);
				this.getAll();
				this.changUpdated.emit(true);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	employeeContactToEdit(employeeContactInfo: IEmployeeContactModel) {
		this.form.patchValue(employeeContactInfo);
		// console.log(this.form.value);
	}

	onDelete(employeeContactInfo: IEmployeeContactModel): void {
		let msg = this.msgTranslate(employeeContactInfo.contactType, employeeContactInfo.contactType);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(employeeContactInfo);
		});
	}

	delete(employeeContactInfo: IEmployeeContactModel) {
		this.showLoading();
		this.employeeContactService.Delete(employeeContactInfo.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				this.getAll();
				if (employeeContactInfo.id == this.formCtrls.id.value) this.clearForm();
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	clearForm() {
		this.form.reset({ id: 0 });
		this.submitted = false;
	}

	getAllContactTypes() {
		this.showLoading();
		this.contactService.getAllTypes().subscribe((res: IResponse<Idropdown[]>) => {
			if ((res.message.messageType = MessageTypes.Success)) {
				this.contactTypesList = res.data;
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	showCategories(): boolean {
		// show if select type (category or social accounts)
		return false;
	}

	getAllCategoriesByTypeId(values: Idropdown[]) {
		this.formCtrls.CONTACT_Category_ID.setValue(null);
		let typeId: number = values.length > 0 ? Number(values[0].id) : 0;
		if (typeId > 0) {
			this.showLoading();
			this.contactService
				.getAllCategoriesByTypeId(typeId)
				.subscribe((res: IResponse<Idropdown[]>) => {
					if ((res.message.messageType = MessageTypes.Success)) {
						this.contactCategoryList = res.data;
					} else this.errorList(res.message.messages);
					this.hideLoading();
				});
			this.formCtrls.CONTACT_Category_ID.enable();
		} else this.formCtrls.CONTACT_Category_ID.disable();
	}
}
