import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import {
	IEmployeePersonalInfoModel,
	IEmployeePersonalInfoRequest,
} from "app/personnel-setup/models/employee-personal-info";
import { Idropdown } from "app/shared/interfaces/dropdown.interface";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { CustomValidators } from "ngx-custom-validators";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { IResponse } from "app/shared/interfaces/responces";
import { EmployeePersonalInfoService } from "app/personnel-setup/services/employees/employee-personal-info.service";
import { MaritalStatusService } from "app/hr-setup/services/marital-status.service";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";

@Component({
	selector: "app-employee-personal-info-dialog",
	templateUrl: "./emp-personal-info-dialog.component.html",
	styleUrls: ["./emp-personal-info-dialog.component.scss"],
})
export class EmployeePersonalInfoDialogComponent extends AppHelpers implements OnInit {
	employeeId: number = 0;

	form: FormGroup;
	submitted: boolean = false;
	updated: boolean = false;

	maritalStatusList: Idropdown[];

	constructor(
		private employeePersonalInfoService: EmployeePersonalInfoService,
		private maritalStatusService: MaritalStatusService,
		@Inject(MAT_DIALOG_DATA)
		public data: {
			employeePersonalData: IEmployeePersonalInfoModel;
		},
		public dialogRef: MatDialogRef<EmployeePersonalInfoDialogComponent>
	) {
		super();
		if (data.employeePersonalData) {
			this.employeeId = data.employeePersonalData.employeeId;
		}
	}

	ngOnInit(): void {
		this.form = this.fb.group({
			id: [0, { nonNullable: true }],
			dob: [null, [Validators.required, CustomValidators.date]],
			placeOfBirth: [null],
			maritalID: [null, [Validators.required]],
		});

		if (this.data.employeePersonalData?.id > 0 && this.checkNoBusinessOrCompany())
			this.employeeToEdit(this.data.employeePersonalData);

		this.dialogRef.backdropClick().subscribe((ele) => {
			this.onCloseDialog();
		});
	}

	onCloseDialog() {
		this.dialogRef.close(this.updated);
	}

	get formCtrls() {
		return this.form.controls;
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) {
			this.getAllMaritalStatus();
		}
	}

	submit() {
		// console.log(this.form.value);
		this.submitted = true;
		if (this.form.valid) {
			const model: IEmployeePersonalInfoRequest = {
				PersonID: this.employeeId,
				id: this.form.value.id,
				dob: HelpersFunctions.mapDateReverse(this.form.value.dob),
				placeOfBirth: HelpersFunctions.mapDateReverse(this.form.value.placeOfBirth),
				nationality: this.form.value.nationality,
				maritalID: this.form.value.maritalID,
				nationalid: this.form.value.nationalid,
			};

			model.id == 0 ? this.create(model) : this.update(model);
		} // else this.toastr.error("General.formNotValid");
	}

	create(model: IEmployeePersonalInfoRequest) {
		this.showLoading();
		this.employeePersonalInfoService
			.create(model)
			.subscribe((res: IResponse<IEmployeePersonalInfoModel>) => {
				if (res?.message?.messageType == MessageTypes.Success) {
					this.addSuccessfullyMsg(res?.message?.messageTypeName);
					// this.employeeToEdit();
					this.updated = true;
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	update(model: IEmployeePersonalInfoRequest) {
		this.showLoading();
		this.employeePersonalInfoService
			.update(model)
			.subscribe((res: IResponse<IEmployeePersonalInfoModel>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.updateSuccessfullyMsg(res?.message?.messageTypeName);
					// this.employeeToEdit();
					this.updated = true;
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	employeeToEdit(employeePersonalInfo: IEmployeePersonalInfoModel) {
		this.form.patchValue(employeePersonalInfo);
		// console.log(this.form.value);
	}

	getAllMaritalStatus() {
		this.maritalStatusService.getAll().subscribe((res: IResponse<Idropdown[]>) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.maritalStatusList = res.data;
			} else this.errorList(res.message.messages);
		});
	}
}
