import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import {
  IEmployeeSapB1Model,
  IEmployeeSapB1Request,
} from "app/personnel-setup/models/employee-sap-integration";
import { EmployeeSapB1Service } from "app/personnel-setup/services/employees/employee-sap-b1.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-emp-sap-b1-dialog",
  templateUrl: "./emp-sap-b1-dialog.component.html",
  styleUrls: ["./emp-sap-b1-dialog.component.scss"],
})
export class EmpSapB1DialogComponent extends AppHelpers implements OnInit {
  employeeId: number = 0;
  updated: boolean = false;

  mandatoryColomns: string[] = [
    "Ser",
    // "id",
    "systemInstance",
    "projectCode",
    "dimintion",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmployeeSapB1Model>;

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;

  constructor(
    private employeeSapB1Service: EmployeeSapB1Service,
    @Inject(MAT_DIALOG_DATA) public data: { employeeId: number },
    public dialogRef: MatDialogRef<EmpSapB1DialogComponent>
  ) {
    super();
    this.employeeId = data.employeeId;
  }

  ngOnInit(): void {
    this.form = this.fb.group(
      {
        id: [0, { nonNullable: true }],
        systemInstanceId: [null, [Validators.required]],
        projectCode: [null, [Validators.required]],
        dimintion: [null],
      }
    );

    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  onCloseDialog() {
    this.dialogRef.close(this.updated);
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      if (this.employeeId > 0) this.getAll();
    }
  }

  getAll() {
    this.employeeSapB1Service
      .getAllByEmployeeId(this.employeeId)
      .subscribe((res: IResponse<IEmployeeSapB1Model[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmployeeSapB1Request = {
        Person_ID: this.employeeId,
        id: this.form.value.id,
        systemInstanceId: this.form.value.systemInstanceId,
        projectCode: this.form.value.projectCode,
        dimintion: this.form.value.dimintion,
      };

      model.id == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmployeeSapB1Request) {
    this.showLoading();
    this.employeeSapB1Service
      .create(model)
      .subscribe((res: IResponse<IEmployeeSapB1Model>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IEmployeeSapB1Request) {
    this.showLoading();
    this.employeeSapB1Service
      .update(model)
      .subscribe((res: IResponse<IEmployeeSapB1Model>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  employeeSapB1ToEdit(employeeSapB1Info: IEmployeeSapB1Model) {
    this.form.patchValue(employeeSapB1Info);
    // console.log(this.form.value);
  }

  onDelete(employeeSapB1Info: IEmployeeSapB1Model): void {
    let msg = this.msgTranslate(employeeSapB1Info.systemInstance);
    this.deleateDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(employeeSapB1Info);
    });
  }

  delete(employeeSapB1Info: IEmployeeSapB1Model) {
    this.showLoading();
    this.employeeSapB1Service
      .Delete(employeeSapB1Info.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          if (employeeSapB1Info.id == this.formCtrls.id.value) this.clearForm();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }
}
