import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { IMilitryGradeModel, IMilitryStatusModel } from "../models/militry";

@Injectable({
  providedIn: "root",
})
export class MilitryService extends AppClientService {
  constructor() {
    super();
  }

  getAllMilitryStatus() {
    return this.get<IResponse<IMilitryStatusModel[]>>(
      "HRSetup/MilitryStatus/GetAll"
    );
  }

  getAllMilitryGrades() {
    return this.get<IResponse<IMilitryGradeModel[]>>(
      "HRSetup/MilitryGrade/GetAll"
    );
  }
}
