import { Component, Input, OnInit } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import {
	IEmployeeAddressModel,
	IEmployeeContactModel,
} from "app/personnel-setup/models/employee-personal-info";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmployeeContactAndAddressDialogComponent } from "../emp-contact-and-address-dialog/emp-contact-and-address-dialog.component";
import { EmployeeContactService } from "app/personnel-setup/services/employees/employee-contact.service";
import { EmployeeAddressService } from "app/personnel-setup/services/employees/employee-address.service";
import { forkJoin } from "rxjs/observable/forkJoin";
import { MessageTypes } from "app/shared/enums/message-types.enum";

@Component({
	selector: "emp-contact-and-address-card",
	templateUrl: "./emp-contact-and-address-card.component.html",
	styleUrls: ["./emp-contact-and-address-card.component.scss"],
})
export class EmployeeContactAndAddressCardComponent extends AppHelpers implements OnInit {
	@Input() employeeId: number;
	employeeContactInfo: IEmployeeContactModel;
	employeeAddressInfo: IEmployeeAddressModel;

	isLoaded: boolean = false;

	constructor(
		private employeeContactService: EmployeeContactService,
		private employeeAddressService: EmployeeAddressService,
		private dialog: MatDialog
	) {
		super();
	}

	ngOnInit(): void {}

	override onChangeBusinessAndCompanyId(): void {
		this.employeeContactInfo = null;
		this.employeeAddressInfo = null;
		if (this.employeeId > 0 && this.checkNoBusinessOrCompany()) {
			this.getAllData();
		} else this.isLoaded = true;
	}

	getEmployeeContactInfoByEmpIdApi() {
		return this.employeeContactService.getAllByEmployeeId(this.employeeId);
	}
	getEmployeeAddressInfoByEmpIdApi() {
		return this.employeeAddressService.getAllByEmployeeId(this.employeeId);
	}

	getAllData() {
		this.employeeContactInfo = null;
		this.employeeAddressInfo = null;
		forkJoin([
			this.getEmployeeAddressInfoByEmpIdApi(),
			this.getEmployeeContactInfoByEmpIdApi(),
		]).subscribe(([response1, response2]) => {
			if (response1.message.messageType == MessageTypes.Success) {
				// to select one to display
				this.employeeAddressInfo = response1.data[0];
			} else this.errorList(response1.message.messages);

			if (response2.message.messageType == MessageTypes.Success) {
				// to select one to display
				this.employeeContactInfo = response2.data[0];
			} else this.errorList(response2.message.messages);

			this.isLoaded = true;
		});
	}

	openAddEditDialog(): void {
		this.dialog
			.open(EmployeeContactAndAddressDialogComponent, {
				data: {
					employeeId: this.employeeId,
				},
				position: {
					top: "40px",
				},
				minWidth: "600px",
				panelClass: "dialog-emp-form",
				autoFocus: false,
			})
			.afterClosed()
			.subscribe((updated: boolean) => {
				if (updated) {
					this.isLoaded = false;
					this.getAllData();
				}
			});
	}
}
