import { Injectable } from "@angular/core";
import {
  IEmpPermissionModel,
  IEmpPermissionRequest,
} from "app/personnel-setup/models/employee-balances";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeePermissionService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmpPermissionRequest) {
    return this.post<IResponse<IEmpPermissionModel>>(
      "Personnel/Employees/Create_Permission",
      data
    );
  }

  update(data: IEmpPermissionRequest) {
    return this.post<IResponse<IEmpPermissionModel>>(
      "Personnel/Employees/Edit_Permission",
      data
    );
  }

  getAllByEmployeeId(employeeId: number) {
    return this.get<IResponse<IEmpPermissionModel[]>>(
      "Personnel/Employees/GetAll_Permission",
      {
        params: {
          $filter: `id eq ${employeeId}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Personnel/Employees/Delete_Permission", {
      params: { Id: Id },
    });
  }
}
