import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { IStatusEventModel, IStatusModel } from "../models/emp-job-status";

@Injectable({
  providedIn: "root",
})
export class EmpJobStatusService extends AppClientService {
  constructor() {
    super();
  }

  getAllStatus() {
    return this.get<IResponse<IStatusModel[]>>("HRSetup/Status/GetAll");
  }

  getAllStatusEvents(statusId: number) {
    return this.get<IResponse<IStatusEventModel[]>>(
      "HRSetup/Status/GetAll_StatusProperty",
      {
        params: { $filter: `statusID eq ${statusId}` },
      }
    );
  }
}
