import {
  IEmpContractDetailsModel,
  IEmpContractDetailsRequest,
} from "app/personnel-setup/models/employee-employment-info";
import { Component, OnInit, Input, EventEmitter, Output } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { FormGroup, Validators } from "@angular/forms";
import { IContractTypeModel } from "app/hr-setup/models/contract-type";
import { ContractTypeService } from "app/hr-setup/services/contract-type.service";
import { EmployeeContractDetailsService } from "app/personnel-setup/services/employees/employee-contract-details.service";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
  selector: "app-emp-contract-details-dialog",
  templateUrl: "./emp-contract-details-dialog.component.html",
  styleUrls: ["./emp-contract-details-dialog.component.scss"],
})
export class EmpContractDetailsDialogComponent
  extends AppHelpers
  implements OnInit
{
  @Input() employeeId: number = 0;
  @Output() changUpdated = new EventEmitter<boolean>();

  mandatoryColomns: string[] = [
    "Ser",
    // "id",
    "contractType",
    "start_date",
    "end_date",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmpContractDetailsModel>;

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;

  contractTypeList: IContractTypeModel[];

  constructor(
    private employeeContractDetailsService: EmployeeContractDetailsService,
    private contractTypeService: ContractTypeService
  ) {
    super();
  }

  ngOnInit(): void {
    this.form = this.fb.group(
      {
        id: [0, { nonNullable: true }],
        CONTRACT_TYPE_ID: [null, [Validators.required]],
        start_date: [null, [Validators.required]],
        end_date: [null, [Validators.required]],
        attachment: [null],
      },
      {
        validator: CustomValidator.compareDates("start_date", "end_date"),
      }
    );
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      this.getAllContractTypes();
      if (this.employeeId > 0) this.getAll();
    }
  }

  getAll() {
    this.employeeContractDetailsService
      .getAllByEmployeeId(this.employeeId)
      .subscribe((res: IResponse<IEmpContractDetailsModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmpContractDetailsRequest = {
        Person_ID: this.employeeId,
        id: this.form.value.id,
        CONTRACT_TYPE_ID: this.form.value.CONTRACT_TYPE_ID,
        start_date: this.form.value.start_date,
        end_date: this.form.value.end_date,
        attachment: this.form.value.attachment,
      };

      model.id == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmpContractDetailsRequest) {
    this.showLoading();
    this.employeeContractDetailsService
      .create(model)
      .subscribe((res: IResponse<IEmpContractDetailsModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.changUpdated.emit(true);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IEmpContractDetailsRequest) {
    this.showLoading();
    this.employeeContractDetailsService
      .update(model)
      .subscribe((res: IResponse<IEmpContractDetailsModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.changUpdated.emit(true);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  employeeContractDetailsToEdit(
    employeeContractDetails: IEmpContractDetailsModel
  ) {
    this.form.patchValue(employeeContractDetails);
    // console.log(this.form.value);
  }

  onDelete(employeeContractDetails: IEmpContractDetailsModel): void {
    let msg = this.msgTranslate(employeeContractDetails.contract);
    this.deleateDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(employeeContractDetails);
    });
  }

  delete(employeeContractDetails: IEmpContractDetailsModel) {
    this.showLoading();
    this.employeeContractDetailsService
      .Delete(employeeContractDetails.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          if (employeeContractDetails.id == this.formCtrls.id.value)
            this.clearForm();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }

  getAllContractTypes() {
    this.contractTypeService
      .getAll()
      .subscribe((res: IResponse<IContractTypeModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.contractTypeList = res.data;
        } else this.errorList(res.message.messages);
      });
  }
}
