import { Component, OnInit, Inject } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import {
  IEmpCustodyModel,
  IEmpCustodyRequest,
} from "app/personnel-setup/models/employee-employment-info";
import { EmployeeCustodiesService } from "app/personnel-setup/services/employees/employee-custodies.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
  selector: "app-emp-custodies-dialog",
  templateUrl: "./emp-custodies-dialog.component.html",
  styleUrls: ["./emp-custodies-dialog.component.scss"],
})
export class EmpCustodiesDialogComponent extends AppHelpers implements OnInit {
  employeeId: number = 0;
  updated: boolean = false;

  mandatoryColomns: string[] = [
    "Ser",
    // "id",
    "custodies",
    "froM_DATE",
    "endDate",
    "note",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmpCustodyModel>;

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;

  constructor(
    private employeeCustodiesService: EmployeeCustodiesService,
    @Inject(MAT_DIALOG_DATA) public data: { employeeId: number },
    public dialogRef: MatDialogRef<EmpCustodiesDialogComponent>
  ) {
    super();
    this.employeeId = data.employeeId;
  }

  ngOnInit(): void {
    this.form = this.fb.group(
      {
        id: [0, { nonNullable: true }],
        custodyId: [null, [Validators.required]],
        froM_DATE: [null, [Validators.required]],
        endDate: [null, [Validators.required]],
        note: [null],
      },
      {
        validator: CustomValidator.compareDates("froM_DATE", "endDate"),
      }
    );

    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  onCloseDialog() {
    this.dialogRef.close(this.updated);
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      if (this.employeeId > 0) this.getAll();
    }
  }

  getAll() {
    this.employeeCustodiesService
      .getAllByEmployeeId(this.employeeId)
      .subscribe((res: IResponse<IEmpCustodyModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmpCustodyRequest = {
        Person_ID: this.employeeId,
        id: this.form.value.id,
        custodyId: this.form.value.custodyId,
        froM_DATE: this.form.value.froM_DATE,
        endDate: this.form.value.endDate,
        note: this.form.value.note,
      };

      model.id == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmpCustodyRequest) {
    this.showLoading();
    this.employeeCustodiesService
      .create(model)
      .subscribe((res: IResponse<IEmpCustodyModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IEmpCustodyRequest) {
    this.showLoading();
    this.employeeCustodiesService
      .update(model)
      .subscribe((res: IResponse<IEmpCustodyModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  employeeCustodyToEdit(employeeCustodyInfo: IEmpCustodyModel) {
    this.form.patchValue(employeeCustodyInfo);
    // console.log(this.form.value);
  }

  onDelete(employeeCustodyInfo: IEmpCustodyModel): void {
    let msg = this.msgTranslate(employeeCustodyInfo.custodies);
    this.deleateDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(employeeCustodyInfo);
    });
  }

  delete(employeeCustodyInfo: IEmpCustodyModel) {
    this.showLoading();
    this.employeeCustodiesService
      .Delete(employeeCustodyInfo.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          if (employeeCustodyInfo.id == this.formCtrls.id.value)
            this.clearForm();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }
}
