import { Component, OnInit, Inject } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import { ICountryModel } from "app/hr-setup/models/country";
import { IGovernorateModel } from "app/hr-setup/models/governorate";
import { GovernorateService } from "app/hr-setup/services/governorate.service";
import {
  IEmpHiringDocumentModel,
  IEmpHiringDocumentRequest,
} from "app/personnel-setup/models/employee-employment-info";
import { EmployeeHiringDocumentService } from "app/personnel-setup/services/employees/employee-hiring-document.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "emp-hiring-documents-dialog",
  templateUrl: "./emp-hiring-documents-dialog.component.html",
  styleUrls: ["./emp-hiring-documents-dialog.component.scss"],
})
export class EmpHiringDocumentsDialogComponent
  extends AppHelpers
  implements OnInit
{
  employeeId: number = 0;
  updated: boolean = false;

  mandatoryColomns: string[] = [
    "Ser",
    // "id",
    "country",
    "governorate",
    "address",
    "froM_DATE",
    "tO_DATE",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmpHiringDocumentModel>;

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;

  governorateList: IGovernorateModel[];

  constructor(
    private employeeHiringDocumentService: EmployeeHiringDocumentService,
    private governorateService: GovernorateService,
    @Inject(MAT_DIALOG_DATA) public data: { employeeId: number },
    public dialogRef: MatDialogRef<EmpHiringDocumentsDialogComponent>
  ) {
    super();
    this.employeeId = data.employeeId;
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      documentName: [null, [Validators.required]],
      file: [null, [Validators.required]],
      isHandDelivered: [null],
      notCounted: [null],
    });

    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  onCloseDialog() {
    this.dialogRef.close(this.updated);
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany()) {
      this.getAll();
    }
  }

  getAll() {
    this.employeeHiringDocumentService
      .getAllByEmployeeId(this.employeeId)
      .subscribe((res: IResponse<IEmpHiringDocumentModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmpHiringDocumentRequest = {
        Person_ID: this.employeeId,
        id: this.form.value.id,
        documentName: this.form.value.documentName,
        file: this.form.value.file,
        isHandDelivered: this.form.value.isHandDelivered ?? false,
        notCounted: this.form.value.notCounted ?? false,
      };

      model.id == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmpHiringDocumentRequest) {
    this.showLoading();
    this.employeeHiringDocumentService
      .create(model)
      .subscribe((res: IResponse<IEmpHiringDocumentModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IEmpHiringDocumentRequest) {
    this.showLoading();
    this.employeeHiringDocumentService
      .update(model)
      .subscribe((res: IResponse<IEmpHiringDocumentModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  employeeHiringDocumentToEdit(
    employeeHiringDocument: IEmpHiringDocumentModel
  ) {
    this.form.patchValue(employeeHiringDocument);
    // console.log(this.form.value);
  }

  onDelete(employeeHiringDocument: IEmpHiringDocumentModel): void {
    let msg = this.msgTranslate(employeeHiringDocument.documentName);
    this.deleateDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(employeeHiringDocument);
    });
  }

  delete(employeeHiringDocument: IEmpHiringDocumentModel) {
    this.showLoading();
    this.employeeHiringDocumentService
      .Delete(employeeHiringDocument.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          if (employeeHiringDocument.id == this.formCtrls.id.value)
            this.clearForm();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }
}
