import { Component, OnInit, Inject } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import {
  IVacationModel,
  IVacationResponse,
} from "app/attendance-setup/models/vacation";
import { VacationService } from "app/attendance-setup/services/vacation/vacation.service";
import {
  IEmpVacationModel,
  IEmpVacationRequest,
} from "app/personnel-setup/models/employee-balances";
import { EmployeeVacationService } from "app/personnel-setup/services/employees/employee-vacation.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
  selector: "emp-vacation-dialog",
  templateUrl: "./emp-vacation-dialog.component.html",
  styleUrls: ["./emp-vacation-dialog.component.scss"],
})
export class EmpVacationDialogComponent extends AppHelpers implements OnInit {
  employeeId: number = 0;
  updated: boolean = false;

  mandatoryColomns: string[] = [
    "Ser",
    // "id",
    "vacation",
    "startDate",
    "endDate",
    "balance",
    "used",
    "remaining",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmpVacationModel>;

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;

  vacationTypeList: IVacationModel[];

  constructor(
    private employeeVacationService: EmployeeVacationService,
    private vacationService: VacationService,
    @Inject(MAT_DIALOG_DATA) public data: { employeeId: number },
    public dialogRef: MatDialogRef<EmpVacationDialogComponent>
  ) {
    super();
    this.employeeId = data.employeeId;
  }

  ngOnInit(): void {
    this.form = this.fb.group(
      {
        id: [0, { nonNullable: true }],
        vacationId: [null, [Validators.required]],
        startDate: [null, [Validators.required]],
        endDate: [null],
        balance: [null, [Validators.required]],
        used: [null, [Validators.required]],
      },
      {
        validator: CustomValidator.compareDates("startDate", "endDate"),
      }
    );

    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  onCloseDialog() {
    this.dialogRef.close(this.updated);
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      this.getAllVacationTypes();
      if (this.employeeId > 0) this.getAll();
    }
  }

  getAll() {
    this.employeeVacationService
      .getAllByEmployeeId(this.employeeId)
      .subscribe((res: IResponse<IEmpVacationModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmpVacationRequest = {
        Person_ID: this.employeeId,
        id: this.form.value.id,
        vacationId: this.form.value.vacationId,
        startDate: this.form.value.startDate,
        endDate: this.form.value.endDate,
        balance: this.form.value.balance,
        used: this.form.value.used,
      };

      model.id == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmpVacationRequest) {
    this.showLoading();
    this.employeeVacationService
      .create(model)
      .subscribe((res: IResponse<IEmpVacationModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IEmpVacationRequest) {
    this.showLoading();
    this.employeeVacationService
      .update(model)
      .subscribe((res: IResponse<IEmpVacationModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  employeeVacationToEdit(employeeVacationInfo: IEmpVacationModel) {
    this.form.patchValue(employeeVacationInfo);
    // console.log(this.form.value);
  }

  onDelete(employeeVacationInfo: IEmpVacationModel): void {
    let msg = this.msgTranslate(employeeVacationInfo.vacation);
    this.deleateDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(employeeVacationInfo);
    });
  }

  delete(employeeVacationInfo: IEmpVacationModel) {
    this.showLoading();
    this.employeeVacationService
      .Delete(employeeVacationInfo.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          if (employeeVacationInfo.id == this.formCtrls.id.value)
            this.clearForm();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }

  getAllVacationTypes() {
    this.vacationService
      .getAll()
      .subscribe((res: IResponse<IVacationResponse[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.vacationTypeList = res.data.map((item) => item.att_Vacation);
        } else this.errorList(res.message.messages);
      });
  }
}
