import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { ICountryModel } from "app/hr-setup/models/country";
import { IGovernorateModel } from "app/hr-setup/models/governorate";
import { GovernorateService } from "app/hr-setup/services/governorate.service";
import {
	IEmployeeAddressModel,
	IEmployeeAddressRequest,
} from "app/personnel-setup/models/employee-personal-info";
import { EmployeeAddressService } from "app/personnel-setup/services/employees/employee-address.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-employee-address-dialog",
	templateUrl: "./emp-address-dialog.component.html",
	styleUrls: ["./emp-address-dialog.component.scss"],
})
export class EmployeeAddressDialogComponent extends AppHelpers implements OnInit {
	@Input() employeeId: number = 0;
	@Output() changUpdated = new EventEmitter<boolean>();

	mandatoryColomns: string[] = [
		"Ser",
		// "id",
		"country",
		"governorate",
		"address",
		"primary",
		"actions",
	];
	optionalColomns: string[] = [];
	dataSource: MatTableDataSource<IEmployeeAddressModel>;

	isLoaded: boolean = false;

	form: FormGroup;
	submitted: boolean = false;

	governorateList: IGovernorateModel[];

	constructor(
		private employeeAddressService: EmployeeAddressService,
		private governorateService: GovernorateService
	) {
		super();
	}

	ngOnInit(): void {
		this.form = this.fb.group({
			id: [0, { nonNullable: true }],
			countryId: [null, [Validators.required]],
			Governorate_ID: [{ value: null, disabled: true }, [Validators.required]],
			address: [null, [Validators.required]],
			IsPrimary: [null],
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.employeeId > 0 && this.checkNoBusinessOrCompany()) {
			this.getAll();
		}
	}

	getAll() {
		this.employeeAddressService
			.getAllByEmployeeId(this.employeeId)
			.subscribe((res: IResponse<IEmployeeAddressModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.dataSource = new MatTableDataSource(res.data);
					this.isLoaded = true;
				} else this.errorList(res.message.messages);
			});
	}

	submit() {
		// console.log(this.form.value);
		this.submitted = true;
		if (this.form.valid) {
			const model: IEmployeeAddressRequest = {
				Person_ID: this.employeeId,
				id: this.form.value.id,
				countryId: this.form.value.countryId,
				Governorate_ID: this.form.value.Governorate_ID,
				address: this.form.value.address,
				IsPrimary: this.form.value.IsPrimary ?? false,
			};

			model.id == 0 ? this.create(model) : this.update(model);
		} // else this.toastr.error("General.formNotValid");
	}

	create(model: IEmployeeAddressRequest) {
		this.showLoading();
		this.employeeAddressService.create(model).subscribe((res: IResponse<IEmployeeAddressModel>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.clearForm();
				this.addSuccessfullyMsg(res?.message?.messageTypeName);
				this.getAll();
				this.changUpdated.emit(true);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	update(model: IEmployeeAddressRequest) {
		this.showLoading();
		this.employeeAddressService.update(model).subscribe((res: IResponse<IEmployeeAddressModel>) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.clearForm();
				this.updateSuccessfullyMsg(res?.message?.messageTypeName);
				this.getAll();
				this.changUpdated.emit(true);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	employeeAddressToEdit(employeeAddressInfo: IEmployeeAddressModel) {
		this.form.patchValue(employeeAddressInfo);
		// console.log(this.form.value);
	}

	onDelete(employeeAddressInfo: IEmployeeAddressModel): void {
		let msg = this.msgTranslate(employeeAddressInfo.country, employeeAddressInfo.country);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(employeeAddressInfo);
		});
	}

	delete(employeeAddressInfo: IEmployeeAddressModel) {
		this.showLoading();
		this.employeeAddressService.Delete(employeeAddressInfo.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				this.getAll();
				if (employeeAddressInfo.id == this.formCtrls.id.value) this.clearForm();
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	clearForm() {
		this.form.reset({ id: 0 });
		this.submitted = false;
	}

	getAllGovernorateByCountryId(values: ICountryModel[]) {
		this.formCtrls.Governorate_ID.setValue(null);
		let countryId: number = values.length > 0 ? values[0].id : 0;
		if (countryId > 0) {
			this.showLoading();
			this.governorateService
				.getAllByCountryId(countryId)
				.subscribe((res: IResponse<IGovernorateModel[]>) => {
					if ((res.message.messageType = MessageTypes.Success)) {
						this.governorateList = res.data;
					} else this.errorList(res.message.messages);
					this.hideLoading();
				});
			this.formCtrls.Governorate_ID.enable();
		} else this.formCtrls.Governorate_ID.disable();
	}
}
