import { Injectable } from "@angular/core";
import {
  IEmpLocationModel,
  IEmpLocationRequest,
} from "app/personnel-setup/models/employee-employment-info";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeLocationService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmpLocationRequest) {
    return this.post<IResponse<IEmpLocationModel>>(
      "Personnel/Employees/Create_location",
      data
    );
  }

  update(data: IEmpLocationRequest) {
    return this.post<IResponse<IEmpLocationModel>>(
      "Personnel/Employees/Edit_location",
      data
    );
  }

  getAllByEmployeeId(employeeId: number) {
    return this.get<IResponse<IEmpLocationModel[]>>(
      "Personnel/Employees/GetAll_location",
      {
        params: {
          $filter: `id eq ${employeeId}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Personnel/Employees/Delete_location", {
      params: { Id: Id },
    });
  }
}
