import { Component, Input, OnInit } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmpWorkScheduleDialogComponent } from "../emp-work-schedule-dialog/emp-work-schedule-dialog.component";
import { EmployeeWorkScheduleService } from "app/personnel-setup/services/employees/employee-work-schedule.service";
import { IEmployeeWorkScheduleModel } from "app/personnel-setup/models/employee-employment-info";

@Component({
  selector: "emp-work-schedule-card",
  templateUrl: "./emp-work-schedule-card.component.html",
  styleUrls: ["./emp-work-schedule-card.component.scss"],
})
export class EmpWorkScheduleCardComponent extends AppHelpers implements OnInit {
  @Input() employeeId: number;
  employeeWorkScheduleInfo: IEmployeeWorkScheduleModel;

  isLoaded: boolean = false;

  constructor(
    private employeeWorkScheduleService: EmployeeWorkScheduleService,
    private dialog: MatDialog
  ) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.employeeWorkScheduleInfo = null;
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany())
      this.getAllByEmployeeId();
    else this.isLoaded = true;
  }

  getAllByEmployeeId() {
    this.employeeWorkScheduleInfo = null;
    this.employeeWorkScheduleService
      .getAllByEmployeeId(this.employeeId)
      .subscribe((res: IResponse<IEmployeeWorkScheduleModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.employeeWorkScheduleInfo = res.data[0];
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialog
      .open(EmpWorkScheduleDialogComponent, {
        data: {
          employeeId: this.employeeId,
        },
        position: {
          top: "40px",
        },
        minWidth: "600px",
        panelClass: "dialog-emp-form",
        autoFocus: false,
      })
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) this.getAllByEmployeeId();
      });
  }
}
