import { Component, Input, OnInit } from "@angular/core";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmployeePermissionService } from "app/personnel-setup/services/employees/employee-permission.service";
import { MatDialog } from "@angular/material/dialog";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IEmpPermissionModel } from "app/personnel-setup/models/employee-balances";
import { EmpPermissionDialogComponent } from "../emp-permission-dialog/emp-permission-dialog.component";

@Component({
  selector: "emp-permission-card",
  templateUrl: "./emp-permission-card.component.html",
  styleUrls: ["./emp-permission-card.component.scss"],
})
export class EmpPermissionCardComponent extends AppHelpers implements OnInit {
  @Input() employeeId: number;
  employeePermissionInfo: IEmpPermissionModel[];

  isLoaded: boolean = false;

  constructor(
    private employeePermissionService: EmployeePermissionService,
    private dialog: MatDialog
  ) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.employeePermissionInfo = null;
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany())
      this.getAllByEmployeeId();
    else this.isLoaded = true;
  }

  getAllByEmployeeId() {
    this.employeePermissionInfo = null;
    this.employeePermissionService
      .getAllByEmployeeId(this.employeeId)
      .subscribe((res: IResponse<IEmpPermissionModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.employeePermissionInfo = res.data;
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialog
      .open(EmpPermissionDialogComponent, {
        data: {
          employeeId: this.employeeId,
        },
        position: {
          top: "40px",
        },
        minWidth: "600px",
        panelClass: "dialog-emp-form",
        autoFocus: false,
      })
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) this.getAllByEmployeeId();
      });
  }
}
