import { Injectable } from "@angular/core";
import {
  IEmployeePayrollModel,
  IEmployeePayrollRequest,
} from "app/personnel-setup/models/payments-information";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeePayrollService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmployeePayrollRequest) {
    return this.post<IResponse<IEmployeePayrollModel>>(
      "Personnel/Employees/Create_address",
      data
    );
  }

  update(data: IEmployeePayrollRequest) {
    return this.post<IResponse<IEmployeePayrollModel>>(
      "Personnel/Employees/Edit_address",
      data
    );
  }

  getAllByEmployeeId(employeeId: number) {
    return this.get<IResponse<IEmployeePayrollModel[]>>(
      "Personnel/Employees/GetAll_address",
      {
        params: {
          $filter: `id eq ${employeeId}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Personnel/Employees/Delete_address", {
      params: { Id: Id },
    });
  }
}
