import { Component, Input, OnInit } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmpJobInfoDialogComponent } from "./../emp-job-info-dialog/emp-job-info-dialog.component";
import {
  IEmpContractDetailsModel,
  IEmpJobDataModel,
} from "app/personnel-setup/models/employee-employment-info";
import { EmployeeJobInformationService } from "app/personnel-setup/services/employees/employee-job-information.service";
import { EmployeeContractDetailsService } from "app/personnel-setup/services/employees/employee-contract-details.service";
import { forkJoin } from "rxjs";

@Component({
  selector: "emp-job-info-card",
  templateUrl: "./emp-job-info-card.component.html",
  styleUrls: ["./emp-job-info-card.component.scss"],
})
export class EmpJobInfoCardComponent extends AppHelpers implements OnInit {
  @Input() employeeId: number;

  employeeJobInfo: IEmpJobDataModel;
  employeeContractDetailsInfo: IEmpContractDetailsModel;

  isLoaded: boolean = false;

  constructor(
    private employeeJobInformationService: EmployeeJobInformationService,
    private employeeContractDetailsService: EmployeeContractDetailsService,
    private dialog: MatDialog
  ) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.employeeJobInfo = null;
    this.employeeContractDetailsInfo = null;
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany()) {
      this.getAllData();
    } else this.isLoaded = true;
  }

  getEmployeeJobInfoByEmpIdApi() {
    return this.employeeJobInformationService.getAllByEmployeeId(
      this.employeeId
    );
  }
  getEmployeeContractDetailsInfoByEmpIdApi() {
    return this.employeeContractDetailsService.getAllByEmployeeId(
      this.employeeId
    );
  }

  getAllData() {
    this.employeeJobInfo = null;
    this.employeeContractDetailsInfo = null;
    forkJoin([
      this.getEmployeeJobInfoByEmpIdApi(),
      this.getEmployeeContractDetailsInfoByEmpIdApi(),
    ]).subscribe(([response1, response2]) => {
      if (response1.message.messageType == MessageTypes.Success) {
        // to select one to display
        this.employeeJobInfo = response1.data[0];
      } else this.errorList(response1.message.messages);

      if (response2.message.messageType == MessageTypes.Success) {
        // to select one to display
        this.employeeContractDetailsInfo = response2.data[0];
      } else this.errorList(response2.message.messages);

      this.isLoaded = true;
    });
  }

  openAddEditDialog(): void {
    this.dialog
      .open(EmpJobInfoDialogComponent, {
        data: {
          employeeId: this.employeeId,
        },
        position: {
          top: "40px",
        },
        minWidth: "600px",
        panelClass: "dialog-emp-form",
        autoFocus: false,
      })
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) {
          this.isLoaded = false;
          this.getAllData();
        }
      });
  }
}
