import { Injectable } from "@angular/core";
import {
  IEmployeeWorkScheduleModel,
  IEmployeeWorkScheduleRequest,
} from "app/personnel-setup/models/employee-employment-info";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeWorkScheduleService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmployeeWorkScheduleRequest) {
    return this.post<IResponse<IEmployeeWorkScheduleModel>>(
      "Personnel/Employees/Create_Vacation",
      data
    );
  }

  update(data: IEmployeeWorkScheduleRequest) {
    return this.post<IResponse<IEmployeeWorkScheduleModel>>(
      "Personnel/Employees/Edit_Vacation",
      data
    );
  }

  getAllByEmployeeId(employeeId: number) {
    return this.get<IResponse<IEmployeeWorkScheduleModel[]>>(
      "Personnel/Employees/GetAll_Vacation",
      {
        params: {
          $filter: `id eq ${employeeId}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Personnel/Employees/Delete_Vacation", {
      params: { Id: Id },
    });
  }
}
