import { Component, OnInit, Inject } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import {
	IEmployeeDocumentModel,
	IEmployeeDocumentRequest,
} from "app/personnel-setup/models/employee-personal-info";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { CustomValidators } from "ngx-custom-validators";
import { EmployeeDocumentsService } from "app/personnel-setup/services/employees/employee-documents.service";
import { IIdentityTypeModel } from "app/hr-setup/models/identity-type";
import { IdentityTypeService } from "app/hr-setup/services/identity-type.service";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
	selector: "app-employee-documents-dialog",
	templateUrl: "./emp-documents-dialog.component.html",
	styleUrls: ["./emp-documents-dialog.component.scss"],
})
export class EmployeeDocumentsDialogComponent extends AppHelpers implements OnInit {
	employeeId: number = 0;
	updated: boolean = false;

	mandatoryColomns: string[] = [
		"Ser",
		// "id",
		"document",
		"issuingDate",
		"expirationDate",
		"documentNumber",
		"attachment",
		"actions",
	];
	optionalColomns: string[] = [];
	dataSource: MatTableDataSource<IEmployeeDocumentModel>;

	isLoaded: boolean = false;

	form: FormGroup;
	submitted: boolean = false;

	documentTypeList: IIdentityTypeModel[];

	constructor(
		private employeeDocumentsService: EmployeeDocumentsService,
		private identityTypeService: IdentityTypeService,
		@Inject(MAT_DIALOG_DATA) public data: { employeeId: number },
		public dialogRef: MatDialogRef<EmployeeDocumentsDialogComponent>
	) {
		super();
		this.employeeId = data.employeeId;
	}

	ngOnInit(): void {
		this.form = this.fb.group(
			{
				id: [0, { nonNullable: true }],
				documentId: [null, [Validators.required]],
				issuingDate: [null, [Validators.required]],
				expirationDate: [null, [CustomValidators.date]],
				documentNumber: [null],
				attachment: [null],
			},
			{
				validator: CustomValidator.compareDates("issuingDate", "expirationDate"),
			}
		);

		this.dialogRef.backdropClick().subscribe((ele) => {
			this.onCloseDialog();
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	onCloseDialog() {
		this.dialogRef.close(this.updated);
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.checkNoBusinessOrCompany()) {
			this.getAllDocumentTypes();
			if (this.employeeId > 0) this.getAll();
		}
	}

	getAll() {
		this.employeeDocumentsService
			.getAllByEmployeeId(this.employeeId)
			.subscribe((res: IResponse<IEmployeeDocumentModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.dataSource = new MatTableDataSource(res.data);
					this.isLoaded = true;
				} else this.errorList(res.message.messages);
			});
	}

	submit() {
		// console.log(this.form.value);
		this.submitted = true;
		if (this.form.valid) {
			const model: IEmployeeDocumentRequest = {
				Person_ID: this.employeeId,
				id: this.form.value.id,
				documentId: this.form.value.documentId,
				issuingDate: this.form.value.issuingDate,
				expirationDate: this.form.value.expirationDate,
				documentNumber: this.form.value.documentNumber,
				attachment: this.form.value.attachment,
			};

			model.id == 0 ? this.create(model) : this.update(model);
		} // else this.toastr.error("General.formNotValid");
	}

	create(model: IEmployeeDocumentRequest) {
		this.showLoading();
		this.employeeDocumentsService
			.create(model)
			.subscribe((res: IResponse<IEmployeeDocumentModel>) => {
				if (res?.message?.messageType == MessageTypes.Success) {
					this.clearForm();
					this.addSuccessfullyMsg(res?.message?.messageTypeName);
					this.getAll();
					this.updated = true;
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	update(model: IEmployeeDocumentRequest) {
		this.showLoading();
		this.employeeDocumentsService
			.update(model)
			.subscribe((res: IResponse<IEmployeeDocumentModel>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.clearForm();
					this.updateSuccessfullyMsg(res?.message?.messageTypeName);
					this.getAll();
					this.updated = true;
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	employeeDocumentToEdit(employeeDocumentInfo: IEmployeeDocumentModel) {
		this.form.patchValue(employeeDocumentInfo);
		// console.log(this.form.value);
	}

	onDelete(employeeDocumentInfo: IEmployeeDocumentModel): void {
		let msg = this.msgTranslate(employeeDocumentInfo.document);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(employeeDocumentInfo);
		});
	}

	delete(employeeDocumentInfo: IEmployeeDocumentModel) {
		this.showLoading();
		this.employeeDocumentsService.Delete(employeeDocumentInfo.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				this.getAll();
				if (employeeDocumentInfo.id == this.formCtrls.id.value) this.clearForm();
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	clearForm() {
		this.form.reset({ id: 0 });
		this.submitted = false;
	}

	getAllDocumentTypes() {
		this.identityTypeService.getAll().subscribe((res: IResponse<IIdentityTypeModel[]>) => {
			if ((res.message.messageType = MessageTypes.Success)) {
				this.documentTypeList = res.data;
			} else this.errorList(res.message.messages);
		});
	}
}
