import { Component, Input, OnInit } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { IEmployeeDocumentModel } from "app/personnel-setup/models/employee-personal-info";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmployeeDocumentsService } from "app/personnel-setup/services/employees/employee-documents.service";
import { EmployeeDocumentsDialogComponent } from "../emp-documents-dialog/emp-documents-dialog.component";

@Component({
	selector: "emp-documents-card",
	templateUrl: "./emp-documents-card.component.html",
	styleUrls: ["./emp-documents-card.component.scss"],
})
export class EmployeeDocumentsCardComponent extends AppHelpers implements OnInit {
	@Input() employeeId: number;
	employeeDocumentInfo: IEmployeeDocumentModel;

	isLoaded: boolean = false;

	constructor(
		private employeeDocumentsService: EmployeeDocumentsService,
		private dialog: MatDialog
	) {
		super();
	}

	ngOnInit(): void {}

	override onChangeBusinessAndCompanyId(): void {
		this.employeeDocumentInfo = null;
		if (this.employeeId > 0 && this.checkNoBusinessOrCompany()) this.getAllByEmployeeId();
		else this.isLoaded = true;
	}

	getAllByEmployeeId() {
		this.employeeDocumentInfo = null;
		this.employeeDocumentsService
			.getAllByEmployeeId(this.employeeId)
			.subscribe((res: IResponse<IEmployeeDocumentModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.employeeDocumentInfo = res.data[0];
					this.isLoaded = true;
				} else this.errorList(res.message.messages);
			});
	}

	openAddEditDialog(): void {
		this.dialog
			.open(EmployeeDocumentsDialogComponent, {
				data: {
					employeeId: this.employeeId,
				},
				position: {
					top: "40px",
				},
				minWidth: "600px",
				panelClass: "dialog-emp-form",
				autoFocus: false,
			})
			.afterClosed()
			.subscribe((updated: boolean) => {
				if (updated) this.getAllByEmployeeId();
			});
	}
}
