import { Component, Input, OnInit } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmpBankingAccountDataDialogComponent } from "../emp-banking-account-data-dialog/emp-banking-account-data-dialog.component";
import { IEmployeeBankingAccountModel } from "app/personnel-setup/models/payments-information";
import { EmployeeBankingAccountService } from "app/personnel-setup/services/employees/employee-banking-account.service";

@Component({
  selector: "app-emp-banking-account-data-card",
  templateUrl: "./emp-banking-account-data-card.component.html",
  styleUrls: ["./emp-banking-account-data-card.component.scss"],
})
export class EmpBankingAccountDataCardComponent
  extends AppHelpers
  implements OnInit
{
  @Input() employeeId: number;
  employeeBankingAccountInfo: IEmployeeBankingAccountModel;

  isLoaded: boolean = false;

  constructor(
    private employeeBankingAccountService: EmployeeBankingAccountService,
    private dialog: MatDialog
  ) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.employeeBankingAccountInfo = null;
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany())
      this.getAllByEmployeeId();
    else this.isLoaded = true;
  }

  getAllByEmployeeId() {
    this.employeeBankingAccountInfo = null;
    this.employeeBankingAccountService
      .getAllByEmployeeId(this.employeeId)
      .subscribe((res: IResponse<IEmployeeBankingAccountModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.employeeBankingAccountInfo = res.data[0];
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialog
      .open(EmpBankingAccountDataDialogComponent, {
        data: {
          employeeId: this.employeeId,
        },
        position: {
          top: "40px",
        },
        minWidth: "600px",
        panelClass: "dialog-emp-form",
        autoFocus: false,
      })
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) this.getAllByEmployeeId();
      });
  }
}
