import { Injectable } from "@angular/core";
import {
  IEmpCustodyModel,
  IEmpCustodyRequest,
} from "app/personnel-setup/models/employee-employment-info";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeCustodiesService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmpCustodyRequest) {
    return this.post<IResponse<IEmpCustodyModel>>(
      "Personnel/Employees/Create_Custody",
      data
    );
  }

  update(data: IEmpCustodyRequest) {
    return this.post<IResponse<IEmpCustodyModel>>(
      "Personnel/Employees/Edit_Custody",
      data
    );
  }

  getAllByEmployeeId(employeeId: number) {
    return this.get<IResponse<IEmpCustodyModel[]>>(
      "Personnel/Employees/GetAll_Custody",
      {
        params: {
          $filter: `id eq ${employeeId}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Personnel/Employees/Delete_Custody", {
      params: { Id: Id },
    });
  }
}
