import { Component, Input, OnInit } from "@angular/core";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { IEmpHiringDocumentModel } from "app/personnel-setup/models/employee-employment-info";
import { EmployeeHiringDocumentService } from "app/personnel-setup/services/employees/employee-hiring-document.service";
import { MatDialog } from "@angular/material/dialog";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { EmpHiringDocumentsDialogComponent } from "../emp-hiring-documents-dialog/emp-hiring-documents-dialog.component";

@Component({
  selector: "emp-hiring-documents-card",
  templateUrl: "./emp-hiring-documents-card.component.html",
  styleUrls: ["./emp-hiring-documents-card.component.scss"],
})
export class EmpHiringDocumentsCardComponent
  extends AppHelpers
  implements OnInit
{
  @Input() employeeId: number;
  employeeHiringDocumentInfo: IEmpHiringDocumentModel[];

  isLoaded: boolean = false;

  constructor(
    private employeeHiringDocumentService: EmployeeHiringDocumentService,
    private dialog: MatDialog
  ) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.employeeHiringDocumentInfo = null;
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany())
      this.getAllByEmployeeId();
    else this.isLoaded = true;
  }

  getAllByEmployeeId() {
    this.employeeHiringDocumentInfo = null;
    this.employeeHiringDocumentService
      .getAllByEmployeeId(this.employeeId)
      .subscribe((res: IResponse<IEmpHiringDocumentModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.employeeHiringDocumentInfo = res.data;
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialog
      .open(EmpHiringDocumentsDialogComponent, {
        data: {
          employeeId: this.employeeId,
        },
        position: {
          top: "40px",
        },
        minWidth: "600px",
        panelClass: "dialog-emp-form",
        autoFocus: false,
      })
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) this.getAllByEmployeeId();
      });
  }
}
