import { Component, Input, OnInit } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { IEmployeeWorkExperienceModel } from "app/personnel-setup/models/employee-career";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmployeeWorkExperienceService } from "app/personnel-setup/services/employees/employee-work-experience.service";
import { EmployeeWorkExperienceDialogComponent } from "../emp-work-experience-dialog/employee-work-experience-dialog.component";

@Component({
  selector: "emp-work-experience-card",
  templateUrl: "./emp-work-experience-card.component.html",
  styleUrls: ["./emp-work-experience-card.component.scss"],
})
export class EmployeeWorkExperienceCardComponent
  extends AppHelpers
  implements OnInit
{
  @Input() employeeId: number;
  employeeWorkExperienceInfo: IEmployeeWorkExperienceModel;

  isLoaded: boolean = false;

  constructor(
    private employeeWorkExperienceService: EmployeeWorkExperienceService,
    private dialog: MatDialog
  ) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.employeeWorkExperienceInfo = null;
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany())
      this.getAllByEmployeeId();
    else this.isLoaded = true;
  }

  getAllByEmployeeId() {
    this.employeeWorkExperienceInfo = null;
    this.employeeWorkExperienceService
      .getAllByEmployeeId(this.employeeId)
      .subscribe((res: IResponse<IEmployeeWorkExperienceModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.employeeWorkExperienceInfo = res.data[0];
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialog
      .open(EmployeeWorkExperienceDialogComponent, {
        data: {
          employeeId: this.employeeId,
        },
        position: {
          top: "40px",
        },
        minWidth: "600px",
        panelClass: "dialog-emp-form",
        autoFocus: false,
      })
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) this.getAllByEmployeeId();
      });
  }
}
