import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import { IInstitutionModel } from "app/hr-setup/models/institution";
import { InstitutionService } from "app/hr-setup/services/institution.service";
import {
  IEmployeeEducationModel,
  IEmployeeEducationRequest,
} from "app/personnel-setup/models/employee-career";
import { EmployeeEducationService } from "app/personnel-setup/services/employees/employee-education.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { Idropdown } from "app/shared/interfaces/dropdown.interface";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { CustomValidators } from "ngx-custom-validators";

@Component({
  selector: "app-employee-education-dialog",
  templateUrl: "./emp-education-dialog.component.html",
  styleUrls: ["./emp-education-dialog.component.scss"],
})
export class EmployeeEducationDialogComponent
  extends AppHelpers
  implements OnInit
{
  employeeId: number = 0;
  updated: boolean = false;

  mandatoryColomns: string[] = [
    "Ser",
    // "id",
    "institution",
    "major",
    "degree",
    "startDate",
    "endDate",
    "grade",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmployeeEducationModel>;

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;

  institutionList: IInstitutionModel[];
  majorList: Idropdown[];
  degreeList: Idropdown[];
  gradeList: Idropdown[];

  constructor(
    private employeeEducationService: EmployeeEducationService,
    private institutionService: InstitutionService,
    @Inject(MAT_DIALOG_DATA) public data: { employeeId: number },
    public dialogRef: MatDialogRef<EmployeeEducationDialogComponent>
  ) {
    super();
    this.employeeId = data.employeeId;
  }

  ngOnInit(): void {
    this.form = this.fb.group(
      {
        id: [0, { nonNullable: true }],
        institutionId: [null, [Validators.required]],
        majorId: [null],
        degreeId: [null, [Validators.required]],
        startDate: [null, [CustomValidators.date]],
        endDate: [null, [CustomValidators.date]],
        gradeId: [null, [Validators.required]],
      },
      {
        validator: CustomValidator.compareDates("startDate", "endDate"),
      }
    );

    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  onCloseDialog() {
    this.dialogRef.close(this.updated);
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      this.getAllInstitutions();
      this.getAllMajors();
      this.getAllDegrees();
      this.getAllGrades();
      if (this.employeeId > 0) this.getAll();
    }
  }

  getAll() {
    this.employeeEducationService
      .getAllByEmployeeId(this.employeeId)
      .subscribe((res: IResponse<IEmployeeEducationModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmployeeEducationRequest = {
        Person_ID: this.employeeId,
        id: this.form.value.id,
        institutionId: this.form.value.institutionId,
        majorId: this.form.value.majorId,
        degreeId: this.form.value.degreeId,
        startDate: this.form.value.startDate,
        endData: this.form.value.endData,
        gradeId: this.form.value.gradeId,
      };

      model.id == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmployeeEducationRequest) {
    this.showLoading();
    this.employeeEducationService
      .create(model)
      .subscribe((res: IResponse<IEmployeeEducationModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IEmployeeEducationRequest) {
    this.showLoading();
    this.employeeEducationService
      .update(model)
      .subscribe((res: IResponse<IEmployeeEducationModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  employeeEducationToEdit(employeeEducationInfo: IEmployeeEducationModel) {
    this.form.patchValue(employeeEducationInfo);
    // console.log(this.form.value);
  }

  onDelete(employeeEducationInfo: IEmployeeEducationModel): void {
    let msg = this.msgTranslate(employeeEducationInfo.institution);
    this.deleateDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(employeeEducationInfo);
    });
  }

  delete(employeeEducationInfo: IEmployeeEducationModel) {
    this.showLoading();
    this.employeeEducationService
      .Delete(employeeEducationInfo.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          if (employeeEducationInfo.id == this.formCtrls.id.value)
            this.clearForm();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }

  getAllInstitutions() {
    this.institutionService
      .getALL()
      .subscribe((res: IResponse<IInstitutionModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.institutionList = res.data;
        } else this.errorList(res.message.messages);
      });
  }

  getAllMajors() {
    this.employeeEducationService
      .getAllMajors()
      .subscribe((res: IResponse<Idropdown[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.majorList = res.data;
        } else this.errorList(res.message.messages);
      });
  }

  getAllDegrees() {
    this.employeeEducationService
      .getAllDegrees()
      .subscribe((res: IResponse<Idropdown[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.degreeList = res.data;
        } else this.errorList(res.message.messages);
      });
  }

  getAllGrades() {
    this.employeeEducationService
      .getAllGrades()
      .subscribe((res: IResponse<Idropdown[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.gradeList = res.data;
        } else this.errorList(res.message.messages);
      });
  }
}
