import { Injectable } from "@angular/core";
import {
  IEmployeeMilitaryModel,
  IEmployeeMilitaryRequest,
} from "app/personnel-setup/models/employee-personal-info";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeMilitaryDataService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmployeeMilitaryRequest) {
    return this.post<IResponse<IEmployeeMilitaryModel>>(
      "Personnel/Employees/Create_Military",
      data
    );
  }

  update(data: IEmployeeMilitaryRequest) {
    return this.post<IResponse<IEmployeeMilitaryModel>>(
      "Personnel/Employees/Edit_Military",
      data
    );
  }

  getAllByEmployeeId(employeeId: number) {
    return this.get<IResponse<IEmployeeMilitaryModel[]>>(
      "Personnel/Employees/GetAll_Military",
      {
        params: {
          $filter: `id eq ${employeeId}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Personnel/Employees/Delete_Military", {
      params: { Id: Id },
    });
  }
}
