import { Component, Input, OnInit } from "@angular/core";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmployeeVacationService } from "app/personnel-setup/services/employees/employee-vacation.service";
import { MatDialog } from "@angular/material/dialog";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { EmpVacationDialogComponent } from "../emp-vacation-dialog/emp-vacation-dialog.component";
import { IEmpVacationModel } from "app/personnel-setup/models/employee-balances";

@Component({
  selector: "emp-vacation-card",
  templateUrl: "./emp-vacation-card.component.html",
  styleUrls: ["./emp-vacation-card.component.scss"],
})
export class EmpVacationCardComponent extends AppHelpers implements OnInit {
  @Input() employeeId: number;
  employeeVacationInfo: IEmpVacationModel[];

  isLoaded: boolean = false;

  constructor(
    private employeeVacationService: EmployeeVacationService,
    private dialog: MatDialog
  ) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.employeeVacationInfo = null;
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany())
      this.getAllByEmployeeId();
    else this.isLoaded = true;
  }

  getAllByEmployeeId() {
    this.employeeVacationInfo = null;
    this.employeeVacationService
      .getAllByEmployeeId(this.employeeId)
      .subscribe((res: IResponse<IEmpVacationModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.employeeVacationInfo = res.data;
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialog
      .open(EmpVacationDialogComponent, {
        data: {
          employeeId: this.employeeId,
        },
        position: {
          top: "40px",
        },
        minWidth: "600px",
        panelClass: "dialog-emp-form",
        autoFocus: false,
      })
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) this.getAllByEmployeeId();
      });
  }
}
