import { Component, OnInit } from "@angular/core";
import { FormGroup } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { IEmployeeCardModel } from "app/personnel-setup/models/employees";
import { EmployeesService } from "app/personnel-setup/services/employees/employees.service";
import { megingImgWithColumnData } from "app/shared/components/material-table/material-table.component";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { IRowActionsModel } from "app/shared/interfaces/user-views";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-employees-index",
	templateUrl: "./employees-index.component.html",
	styleUrls: ["./employees-index.component.scss"],
})
export class EmployeesIndexComponent extends AppHelpers implements OnInit {
	mandatoryColomns: string[] = ["Ser", "id", "code", "en_Name", "ar_Name", "actions"];
	optionalColomns: string[] = [
		"jobTitleName",
		"organization",
		"email",
		"status",
		"statusProperty",
		"address",
		"contact",
		"isActive",
	];

	rowFunctionsMap: IRowActionsModel;
	dataSource: MatTableDataSource<IEmployeeCardModel>;
	isLoaded: boolean = false;

	simpleSearchForm: FormGroup;
	advancedSearchForm: FormGroup;

	constructor(private employeesService: EmployeesService) {
		super();
	}

	ngOnInit(): void {
		// if (this.CheckNoBusinessOrCompany()) this.getList(this.currentPage);
		this.simpleSearchForm = this.fb.group({
			employeeId: [null],
			orgTypeId: [null],
			orgId: [null],
		});

		this.advancedSearchForm = this.fb.group({
			name: [null],
			operator: [null],
			value: [null],
		});
	}

	get simpleSearchFormCtrls() {
		return this.simpleSearchForm.controls;
	}

	get advancedSearchFormCtrls() {
		return this.advancedSearchForm.controls;
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.checkNoBusinessOrCompany())
			this.getList(this.pageFromUrl > 0 ? this.pageFromUrl : this.firstPage);
	}

	getList(page: number, pageSize?: number) {
		this.removePageParams();
		this.page.pageIndex = page;
		if (pageSize) this.page.pageSize = pageSize;
		let params: IPageParams = {
			PageNumber: page,
			pageSize: this.page.pageSize,
		};
		this.employeesService
			.getListCard(params)
			.subscribe((res: IResponseList<IEmployeeCardModel[]>) => {
				if (res.message.messageType == 1) {
					if (res.data.length == 0 && page > 1) {
						this.getList(this.prevPage);
						return;
					}

					res.data.map((employee) => {
						employee.jobTitleName = this.msgTranslate(
							employee.jobTitleNameAr,
							employee.jobTitleName
						);
						employee.organization = this.msgTranslate(
							employee.organizationAr,
							employee.organization
						);
						employee.status = this.msgTranslate(employee.statusAr, employee.status);
						employee.statusProperty = this.msgTranslate(
							employee.statusPropertyAr,
							employee.statusProperty
						);
						employee.contact = employee.contacts.map((contact) => contact.value).join(", ");
						employee.address = employee.addresses.join(", ");
            employee.img = employee.img == "/" ? "" : employee.img;
					});
					this.dataSource = new MatTableDataSource(res.data);
					this.isLoaded = true;
					this.page.length = res.meta.totalItemCount;
				} else this.errorList(res.message.messages);
			});
	}

	onDelete(employee: IEmployeeCardModel): void {
		let msg = this.msgTranslate(employee.ar_Name, employee.en_Name);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(employee);
		});
	}

	delete(employee: IEmployeeCardModel) {
		this.showLoading();
		this.employeesService.Delete(employee.id).subscribe((res: IResponse) => {
			if (res.message.messageType == 1) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				if (this.dataSource.data.length == 1 && this.currentPage > 1) {
					// if last item in list: get previous page
					this.getList(this.prevPage, this.page.pageSize);
				} else {
					// if not last item in list: get current page
					this.getList(this.currentPage, this.page.pageSize);
				}
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	//////////// search
	onAction(selectedAction) {
		// console.log(selectedAction);
		if (selectedAction?.name == "simpleSearch") this.toggleSimpleSearchPanel();
		if (selectedAction?.name == "advancedSearch") this.toggleAdvancedSearchPanel();
	}

	// collapsed --> true >> closed & false >> opened
	isSimpleExpanded: boolean = true;
	toggleSimpleSearchPanel() {
		this.isAdvancedExpanded = true;
		this.isSimpleExpanded = !this.isSimpleExpanded;
	}

	isAdvancedExpanded: boolean = true;
	toggleAdvancedSearchPanel() {
		this.isSimpleExpanded = true;
		this.isAdvancedExpanded = !this.isAdvancedExpanded;
	}

  mergingTableColumn: megingImgWithColumnData = {
    isMerge: true,
    columnImgName: "img",
    columnTextName: "en_Name",
  };
}
