import { Component, OnInit, Inject } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import {
	IEmployeeDependantModel,
	IEmployeeDependantRequest,
} from "app/personnel-setup/models/employee-personal-info";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { CustomValidators } from "ngx-custom-validators";
import { Idropdown } from "app/shared/interfaces/dropdown.interface";
import { EmployeeDependantService } from "app/personnel-setup/services/employees/employee-dependant.service";

@Component({
	selector: "app-employee-dependants-dialog",
	templateUrl: "./emp-dependants-dialog.component.html",
	styleUrls: ["./emp-dependants-dialog.component.scss"],
})
export class EmployeeDependantsDialogComponent extends AppHelpers implements OnInit {
	employeeId: number = 0;
	updated: boolean = false;

	mandatoryColomns: string[] = [
		"Ser",
		// "id",
		"country",
		"governorate",
		"address",
		"primary",
		"actions",
	];
	optionalColomns: string[] = [];
	dataSource: MatTableDataSource<IEmployeeDependantModel>;

	isLoaded: boolean = false;

	form: FormGroup;
	submitted: boolean = false;

	relationshipList: Idropdown[];

	constructor(
		private employeeDependantService: EmployeeDependantService,
		@Inject(MAT_DIALOG_DATA) public data: { employeeId: number },
		public dialogRef: MatDialogRef<EmployeeDependantsDialogComponent>
	) {
		super();
		this.employeeId = data.employeeId;
	}

	ngOnInit(): void {
		this.form = this.fb.group({
			id: [0, { nonNullable: true }],
			name: [null, [Validators.required, CustomValidator.whiteSpace]],
			relationshipId: [null, [Validators.required]],
			dob: [null, [CustomValidators.date]],
			phone: [
				null,
				[
					// Validators.required,
					Validators.pattern(/^(\(?\+?[0-9]*\)?)?[0-9_ \-\(\)\S*$]*$/),
					CustomValidator.MobileStartsWith,
					CustomValidator.mobileLength,
				],
			],
			emergencyContact: [null],
		});

		this.dialogRef.backdropClick().subscribe((ele) => {
			this.onCloseDialog();
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	onCloseDialog() {
		this.dialogRef.close(this.updated);
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.checkNoBusinessOrCompany()) {
			this.getAllRelationships();
			if (this.employeeId > 0) this.getAll();
		}
	}

	getAll() {
		this.employeeDependantService
			.getAllByEmployeeId(this.employeeId)
			.subscribe((res: IResponse<IEmployeeDependantModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.dataSource = new MatTableDataSource(res.data);
					this.isLoaded = true;
				} else this.errorList(res.message.messages);
			});
	}

	submit() {
		// console.log(this.form.value);
		this.submitted = true;
		if (this.form.valid) {
			const model: IEmployeeDependantRequest = {
				Person_ID: this.employeeId,
				id: this.form.value.id,
				name: this.form.value.name,
				relationshipId: this.form.value.relationshipId,
				dob: this.form.value.dob,
				phone: this.form.value.phone,
				emergencyContact: this.form.value.emergencyContact ?? false,
			};

			model.id == 0 ? this.create(model) : this.update(model);
		} // else this.toastr.error("General.formNotValid");
	}

	create(model: IEmployeeDependantRequest) {
		this.showLoading();
		this.employeeDependantService
			.create(model)
			.subscribe((res: IResponse<IEmployeeDependantModel>) => {
				if (res?.message?.messageType == MessageTypes.Success) {
					this.clearForm();
					this.addSuccessfullyMsg(res?.message?.messageTypeName);
					this.getAll();
					this.updated = true;
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	update(model: IEmployeeDependantRequest) {
		this.showLoading();
		this.employeeDependantService
			.update(model)
			.subscribe((res: IResponse<IEmployeeDependantModel>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.clearForm();
					this.updateSuccessfullyMsg(res?.message?.messageTypeName);
					this.getAll();
					this.updated = true;
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	employeeDependantToEdit(employeeDependantInfo: IEmployeeDependantModel) {
		this.form.patchValue(employeeDependantInfo);
		// console.log(this.form.value);
	}

	onDelete(employeeDependantInfo: IEmployeeDependantModel): void {
		let msg = this.msgTranslate(employeeDependantInfo.dependantName);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(employeeDependantInfo);
		});
	}

	delete(employeeDependantInfo: IEmployeeDependantModel) {
		this.showLoading();
		this.employeeDependantService.Delete(employeeDependantInfo.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				this.getAll();
				if (employeeDependantInfo.id == this.formCtrls.id.value) this.clearForm();
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	clearForm() {
		this.form.reset({ id: 0 });
		this.submitted = false;
	}

	getAllRelationships() {
		this.employeeDependantService.getAllRelationships().subscribe((res: IResponse<Idropdown[]>) => {
			if ((res.message.messageType = MessageTypes.Success)) {
				this.relationshipList = res.data;
			} else this.errorList(res.message.messages);
		});
	}
}
