import { Component, Input, OnInit } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { IEmployeePersonalInfoModel } from "app/personnel-setup/models/employee-personal-info";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmployeePersonalInfoDialogComponent } from "../emp-personal-info-dialog/emp-personal-info-dialog.component";
import { EmployeePersonalInfoService } from "app/personnel-setup/services/employees/employee-personal-info.service";

@Component({
	selector: "emp-personal-info-card",
	templateUrl: "./emp-personal-info-card.component.html",
	styleUrls: ["./emp-personal-info-card.component.scss"],
})
export class EmployeePersonalInfoCardComponent extends AppHelpers implements OnInit {
	@Input() employeeId: number;

	employeePersonalInfo: IEmployeePersonalInfoModel;

	isLoaded: boolean = false;

	constructor(
		private employeePersonalInfoService: EmployeePersonalInfoService,
		private dialog: MatDialog
	) {
		super();
	}

	ngOnInit(): void {}

	override onChangeBusinessAndCompanyId(): void {
		this.employeePersonalInfo = null;
		if (this.employeeId > 0 && this.checkNoBusinessOrCompany())
			this.getEmployeePersonalInfoByEmpId();
		else this.isLoaded = true;
	}

	getEmployeePersonalInfoByEmpId() {
		this.employeePersonalInfo = null;
		this.employeePersonalInfoService
			.getByEmployeeId(this.employeeId)
			.subscribe((res: IResponse<IEmployeePersonalInfoModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.employeePersonalInfo = res.data[0];
					this.isLoaded = true;
				} else this.errorList(res.message.messages);
			});
	}

	openAddEditDialog(): void {
		this.dialog
			.open(EmployeePersonalInfoDialogComponent, {
				data: {
					employeeId: this.employeeId,
					employee: this.employeePersonalInfo,
				},
				position: {
					top: "40px",
				},
				minWidth: "600px",
				panelClass: "dialog-emp-form",
				autoFocus: false,
			})
			.afterClosed()
			.subscribe((updated: boolean) => {
				if (updated) this.getEmployeePersonalInfoByEmpId();
			});
	}
}
