import { Injectable } from "@angular/core";
import {
  IEmployeePersonalInfoModel,
  IEmployeePersonalInfoRequest,
} from "../../models/employee-personal-info";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeePersonalInfoService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmployeePersonalInfoRequest) {
    return this.post<IResponse<IEmployeePersonalInfoModel>>(
      "Personnel/Employees/Create_PersonalData",
      data
    );
  }

  update(data: IEmployeePersonalInfoRequest) {
    return this.post<IResponse<IEmployeePersonalInfoModel>>(
      "Personnel/Employees/Edit_PersonalData",
      data
    );
  }

  getByEmployeeId(employeeId: number) {
    return this.get<IResponse<IEmployeePersonalInfoModel[]>>(
      "Personnel/Employees/GetAll_PersonalData",
      {
        params: {
          $filter: `id eq ${employeeId}`,
        },
      }
    );
  }
}
