import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import {
  IEmployeeS4HanaModel,
  IEmployeeS4HanaRequest,
} from "app/personnel-setup/models/employee-sap-integration";
import { EmployeeS4HanaService } from "app/personnel-setup/services/employees/employee-s4-hana.service";
import { IS4SystemInstanceModel } from "app/s4-setup/models/s4-system-instance";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { Idropdown } from "app/shared/interfaces/dropdown.interface";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-emp-s4-hana-dialog",
  templateUrl: "./emp-s4-hana-dialog.component.html",
  styleUrls: ["./emp-s4-hana-dialog.component.scss"],
})
export class EmpS4HanaDialogComponent extends AppHelpers implements OnInit {
  employeeId: number = 0;
  updated: boolean = false;

  mandatoryColomns: string[] = [
    "Ser",
    // "id",
    "systemInstance",
    "sapCostCenter",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmployeeS4HanaModel>;

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;

  sapCostCenterList: Idropdown[];

  constructor(
    private employeeS4HanaService: EmployeeS4HanaService,
    @Inject(MAT_DIALOG_DATA) public data: { employeeId: number },
    public dialogRef: MatDialogRef<EmpS4HanaDialogComponent>
  ) {
    super();
    this.employeeId = data.employeeId;
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      systemInstanceId: [null, [Validators.required]],
      sapCostCenter: [null, [Validators.required]],
    });

    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  onCloseDialog() {
    this.dialogRef.close(this.updated);
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      if (this.employeeId > 0) this.getAll();
    }
  }

  getAll() {
    this.employeeS4HanaService
      .getAllByEmployeeId(this.employeeId)
      .subscribe((res: IResponse<IEmployeeS4HanaModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmployeeS4HanaRequest = {
        Person_ID: this.employeeId,
        id: this.form.value.id,
        systemInstanceId: this.form.value.systemInstanceId,
        sapCostCenter: this.form.value.sapCostCenter,
      };

      model.id == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmployeeS4HanaRequest) {
    this.showLoading();
    this.employeeS4HanaService
      .create(model)
      .subscribe((res: IResponse<IEmployeeS4HanaModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IEmployeeS4HanaRequest) {
    this.showLoading();
    this.employeeS4HanaService
      .update(model)
      .subscribe((res: IResponse<IEmployeeS4HanaModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  employeeS4HanaToEdit(employeeS4HanaInfo: IEmployeeS4HanaModel) {
    this.form.patchValue(employeeS4HanaInfo);
    // console.log(this.form.value);
  }

  onDelete(employeeS4HanaInfo: IEmployeeS4HanaModel): void {
    let msg = this.msgTranslate(employeeS4HanaInfo.systemInstance);
    this.deleateDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(employeeS4HanaInfo);
    });
  }

  delete(employeeS4HanaInfo: IEmployeeS4HanaModel) {
    this.showLoading();
    this.employeeS4HanaService
      .Delete(employeeS4HanaInfo.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          if (employeeS4HanaInfo.id == this.formCtrls.id.value)
            this.clearForm();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }

  getAllSapCostCenterListBySystemInstanceId(values: IS4SystemInstanceModel[]) {
    // this.formCtrls.sapCostCenter.setValue(null);
    // let s4SystemInstanceId: number = values.length > 0 ? values[0].id : 0;
    // if (s4SystemInstanceId > 0) {
    //   this.showLoading();
    //   this.governorateService
    //     .getAllByCountryId(s4SystemInstanceId)
    //     .subscribe((res: IResponse<Idropdown[]>) => {
    //       if ((res.message.messageType = MessageTypes.Success)) {
    //         this.sapCostCenterList = res.data;
    //       } else this.errorList(res.message.messages);
    //       this.hideLoading();
    //     });
    //   this.formCtrls.sapCostCenter.enable();
    // } else this.formCtrls.sapCostCenter.disable();
  }
}
