import { Component, OnInit, Inject } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import {
  IEmpSocialInsuranceModel,
  IEmpSocialInsuranceRequest,
} from "app/personnel-setup/models/employee-employment-info";
import { EmployeeSocialInsuranceService } from "app/personnel-setup/services/employees/employee-social-insurance.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { CustomValidators } from "ngx-custom-validators";

@Component({
  selector: "app-emp-social-insurance-dialog",
  templateUrl: "./emp-social-insurance-dialog.component.html",
  styleUrls: ["./emp-social-insurance-dialog.component.scss"],
})
export class EmpSocialInsuranceDialogComponent
  extends AppHelpers
  implements OnInit
{
  employeeId: number = 0;
  updated: boolean = false;

  mandatoryColomns: string[] = [
    "Ser",
    // "id",
    "insurance_No",
    "insurance_Years",
    "insuranceOffice",
    "froM_DATE",
    "endDate",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmpSocialInsuranceModel>;

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;

  constructor(
    private employeeSocialInsuranceService: EmployeeSocialInsuranceService,
    @Inject(MAT_DIALOG_DATA) public data: { employeeId: number },
    public dialogRef: MatDialogRef<EmpSocialInsuranceDialogComponent>
  ) {
    super();
    this.employeeId = data.employeeId;
  }

  ngOnInit(): void {
    this.form = this.fb.group(
      {
        id: [0, { nonNullable: true }],
        insurance_No: [null, [Validators.required, CustomValidators.number]],
        insurance_Years: [null, [Validators.required, CustomValidators.digits]],
        insuranceOffice: [null, [Validators.required]],
        froM_DATE: [null, [Validators.required]],
        endDate: [null, [Validators.required]],
      },
      {
        validator: CustomValidator.compareDates("froM_DATE", "endDate"),
      }
    );

    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  onCloseDialog() {
    this.dialogRef.close(this.updated);
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      if (this.employeeId > 0) this.getAll();
    }
  }

  getAll() {
    this.employeeSocialInsuranceService
      .getAllByEmployeeId(this.employeeId)
      .subscribe((res: IResponse<IEmpSocialInsuranceModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmpSocialInsuranceRequest = {
        Person_ID: this.employeeId,
        id: this.form.value.id,
        insurance_No: this.form.value.insurance_No,
        insurance_Years: this.form.value.insurance_Years,
        insuranceOffice: this.form.value.insuranceOffice,
        froM_DATE: this.form.value.froM_DATE,
        endDate: this.form.value.endDate,
      };

      model.id == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmpSocialInsuranceRequest) {
    this.showLoading();
    this.employeeSocialInsuranceService
      .create(model)
      .subscribe((res: IResponse<IEmpSocialInsuranceModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IEmpSocialInsuranceRequest) {
    this.showLoading();
    this.employeeSocialInsuranceService
      .update(model)
      .subscribe((res: IResponse<IEmpSocialInsuranceModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  employeeSocialInsuranceToEdit(
    employeeSocialInsuranceInfo: IEmpSocialInsuranceModel
  ) {
    this.form.patchValue(employeeSocialInsuranceInfo);
    // console.log(this.form.value);
  }

  onDelete(employeeSocialInsuranceInfo: IEmpSocialInsuranceModel): void {
    let msg = this.msgTranslate(employeeSocialInsuranceInfo.insurance_No);
    this.deleateDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(employeeSocialInsuranceInfo);
    });
  }

  delete(employeeSocialInsuranceInfo: IEmpSocialInsuranceModel) {
    this.showLoading();
    this.employeeSocialInsuranceService
      .Delete(employeeSocialInsuranceInfo.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          if (employeeSocialInsuranceInfo.id == this.formCtrls.id.value)
            this.clearForm();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }
}
