import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { IBloodTypeModel } from "../models/blood-type";
import { map } from "rxjs/operators";

@Injectable({
  providedIn: "root",
})
export class BloodTypeService extends AppClientService {
  constructor() {
    super();
  }

  getAll() {
    return this.get<IResponse<IBloodTypeModel[]>>("BloodType/GetAll").pipe(
      map((res) => {
        res.data.map((item: IBloodTypeModel) => {
          item.en_Name = item.en_NAme;
        });
        return res;
      })
    );
  }
}
