import { Component, Input, OnInit } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { IEmployeeEducationModel } from "app/personnel-setup/models/employee-career";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmployeeEducationService } from "app/personnel-setup/services/employees/employee-education.service";
import { EmployeeEducationDialogComponent } from "../emp-education-dialog/emp-education-dialog.component";

@Component({
	selector: "emp-education-card",
	templateUrl: "./emp-education-card.component.html",
	styleUrls: ["./emp-education-card.component.scss"],
})
export class EmployeeEducationCardComponent extends AppHelpers implements OnInit {
	@Input() employeeId: number;
	employeeEducationInfo: IEmployeeEducationModel;

	isLoaded: boolean = false;

	constructor(
		private employeeEducationService: EmployeeEducationService,
		private dialog: MatDialog
	) {
		super();
	}

	ngOnInit(): void {}

	override onChangeBusinessAndCompanyId(): void {
		this.employeeEducationInfo = null;
		if (this.employeeId > 0 && this.checkNoBusinessOrCompany()) this.getAllByEmployeeId();
		else this.isLoaded = true;
	}

	getAllByEmployeeId() {
		this.employeeEducationInfo = null;
		this.employeeEducationService
			.getAllByEmployeeId(this.employeeId)
			.subscribe((res: IResponse<IEmployeeEducationModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.employeeEducationInfo = res.data[0];
					this.isLoaded = true;
				} else this.errorList(res.message.messages);
			});
	}

	openAddEditDialog(): void {
		this.dialog
			.open(EmployeeEducationDialogComponent, {
				data: {
					employeeId: this.employeeId,
				},
				position: {
					top: "40px",
				},
				minWidth: "600px",
				panelClass: "dialog-emp-form",
				autoFocus: false,
			})
			.afterClosed()
			.subscribe((updated: boolean) => {
				if (updated) this.getAllByEmployeeId();
			});
	}
}
