import { Component, Input, OnInit } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { IEmployeeDependantModel } from "app/personnel-setup/models/employee-personal-info";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmployeeDependantsDialogComponent } from "../emp-dependants-dialog/emp-dependants-dialog.component";
import { EmployeeDependantService } from "app/personnel-setup/services/employees/employee-dependant.service";

@Component({
	selector: "emp-dependants-card",
	templateUrl: "./emp-dependants-card.component.html",
	styleUrls: ["./emp-dependants-card.component.scss"],
})
export class EmployeeDependantsCardComponent extends AppHelpers implements OnInit {
	@Input() employeeId: number;
	employeeDependantInfo: IEmployeeDependantModel;

	isLoaded: boolean = false;

	constructor(
		private employeeDependantService: EmployeeDependantService,
		private dialog: MatDialog
	) {
		super();
	}

	ngOnInit(): void {}

	override onChangeBusinessAndCompanyId(): void {
		this.employeeDependantInfo = null;
		if (this.employeeId > 0 && this.checkNoBusinessOrCompany()) this.getAllByEmployeeId();
		else this.isLoaded = true;
	}

	getAllByEmployeeId() {
		this.employeeDependantInfo = null;
		this.employeeDependantService
			.getAllByEmployeeId(this.employeeId)
			.subscribe((res: IResponse<IEmployeeDependantModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.employeeDependantInfo = res.data[0];
					this.isLoaded = true;
				} else this.errorList(res.message.messages);
			});
	}

	openAddEditDialog(): void {
		this.dialog
			.open(EmployeeDependantsDialogComponent, {
				data: {
					employeeId: this.employeeId,
				},
				position: {
					top: "40px",
				},
				minWidth: "600px",
				panelClass: "dialog-emp-form",
				autoFocus: false,
			})
			.afterClosed()
			.subscribe((updated: boolean) => {
				if (updated) this.getAllByEmployeeId();
			});
	}
}
