import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import { IAttendancePlanModel } from "app/attendance-setup/models/plan";
import { AttendancePlanService } from "app/attendance-setup/services/attendance-plan.service";
import {
  IEmployeeWorkScheduleModel,
  IEmployeeWorkScheduleRequest,
} from "app/personnel-setup/models/employee-employment-info";
import { EmployeeWorkScheduleService } from "app/personnel-setup/services/employees/employee-work-schedule.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "emp-work-schedule-dialog",
  templateUrl: "./emp-work-schedule-dialog.component.html",
  styleUrls: ["./emp-work-schedule-dialog.component.scss"],
})
export class EmpWorkScheduleDialogComponent
  extends AppHelpers
  implements OnInit
{
  employeeId: number = 0;
  updated: boolean = false;

  mandatoryColomns: string[] = [
    "Ser",
    // "id",
    "plan",
    "startDate",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmployeeWorkScheduleModel>;

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;

  planList: IAttendancePlanModel[];

  constructor(
    private employeeWorkScheduleService: EmployeeWorkScheduleService,
    private attendancePlanService: AttendancePlanService,
    @Inject(MAT_DIALOG_DATA) public data: { employeeId: number },
    public dialogRef: MatDialogRef<EmpWorkScheduleDialogComponent>
  ) {
    super();
    this.employeeId = data.employeeId;
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      planId: [null, [Validators.required]],
      startDate: [null, [Validators.required]],
    });

    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  onCloseDialog() {
    this.dialogRef.close(this.updated);
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      this.getAllPlans();
      if (this.employeeId > 0) this.getAll();
    }
  }

  getAll() {
    this.employeeWorkScheduleService
      .getAllByEmployeeId(this.employeeId)
      .subscribe((res: IResponse<IEmployeeWorkScheduleModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmployeeWorkScheduleRequest = {
        Person_ID: this.employeeId,
        id: this.form.value.id,
        planId: this.form.value.planId,
        startDate: this.form.value.startDate,
      };

      model.id == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmployeeWorkScheduleRequest) {
    this.showLoading();
    this.employeeWorkScheduleService
      .create(model)
      .subscribe((res: IResponse<IEmployeeWorkScheduleModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IEmployeeWorkScheduleRequest) {
    this.showLoading();
    this.employeeWorkScheduleService
      .update(model)
      .subscribe((res: IResponse<IEmployeeWorkScheduleModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  employeeWorkScheduleToEdit(
    employeeWorkScheduleInfo: IEmployeeWorkScheduleModel
  ) {
    this.form.patchValue(employeeWorkScheduleInfo);
    // console.log(this.form.value);
  }

  onDelete(employeeWorkScheduleInfo: IEmployeeWorkScheduleModel): void {
    let msg = this.msgTranslate(employeeWorkScheduleInfo.plan);
    this.deleateDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(employeeWorkScheduleInfo);
    });
  }

  delete(employeeWorkScheduleInfo: IEmployeeWorkScheduleModel) {
    this.showLoading();
    this.employeeWorkScheduleService
      .Delete(employeeWorkScheduleInfo.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          if (employeeWorkScheduleInfo.id == this.formCtrls.id.value)
            this.clearForm();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }

  getAllPlans() {
    this.attendancePlanService
      .getAll()
      .subscribe((res: IResponse<IAttendancePlanModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.planList = res.data;
        } else this.errorList(res.message.messages);
      });
  }
}
