import { Injectable } from "@angular/core";
import { Idropdown } from "app/shared/interfaces/dropdown.interface";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class MaritalStatusService extends AppClientService {
  constructor() {
    super();
  }

  getAll() {
    return this.get<IResponse<Idropdown[]>>("HRSetup/MaritalStatus/GetAll");
  }
}
