import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { pathMatch } from "app/shared/enums/route-path-match.enum";
import { ViewsManager } from "app/shared/resources/views-manager";
import { AddEmployeeComponent } from "./components/employees/add-employee/add-employee.component";
import { EmployeesIndexComponent } from "./components/employees/employees-index/employees-index.component";

const routes: Routes = [
	{
		path: "employees",
		data: { view: ViewsManager.employees },
		children: [
			{
				path: "",
				redirectTo: "index",
				pathMatch: pathMatch.full,
			},
			{
				path: "index",
				component: EmployeesIndexComponent,
				data: { view: ViewsManager.employees_index },
			},
			{
				path: "add",
				component: AddEmployeeComponent,
				data: { view: ViewsManager.employee_profile },
			},
			{
				path: "edit/:employeeId",
				component: AddEmployeeComponent,
				data: { view: ViewsManager.employee_profile },
			},
		],
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class PersonnelSetupRoutingModule {}
