import { Injectable } from "@angular/core";
import {
  IEmployeeWorkExperienceModel,
  IEmployeeWorkExperienceRequest,
} from "app/personnel-setup/models/employee-career";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeWorkExperienceService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmployeeWorkExperienceRequest) {
    return this.post<IResponse<IEmployeeWorkExperienceModel>>(
      "Personnel/Employees/Create_WorkExperience",
      data
    );
  }

  update(data: IEmployeeWorkExperienceRequest) {
    return this.post<IResponse<IEmployeeWorkExperienceModel>>(
      "Personnel/Employees/Edit_WorkExperience",
      data
    );
  }

  getAllByEmployeeId(employeeId: number) {
    return this.get<IResponse<IEmployeeWorkExperienceModel[]>>(
      "Personnel/Employees/GetAll_WorkExperience",
      {
        params: {
          $filter: `id eq ${employeeId}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Personnel/Employees/Delete_WorkExperience", {
      params: { Id: Id },
    });
  }
}
