import { MatDialog } from "@angular/material/dialog";
import { Component, Input, OnInit } from "@angular/core";
import { IEmpSocialInsuranceModel } from "app/personnel-setup/models/employee-employment-info";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { EmpSocialInsuranceDialogComponent } from "../emp-social-insurance-dialog/emp-social-insurance-dialog.component";
import { EmployeeSocialInsuranceService } from "app/personnel-setup/services/employees/employee-social-insurance.service";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-emp-social-insurance-card",
  templateUrl: "./emp-social-insurance-card.component.html",
  styleUrls: ["./emp-social-insurance-card.component.scss"],
})
export class EmpSocialInsuranceCardComponent
  extends AppHelpers
  implements OnInit
{
  @Input() employeeId: number;
  employeeSocialInsuranceInfo: IEmpSocialInsuranceModel;

  isLoaded: boolean = false;

  constructor(
    private employeeSocialInsuranceService: EmployeeSocialInsuranceService,
    private dialog: MatDialog
  ) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.employeeSocialInsuranceInfo = null;
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany())
      this.getAllByEmployeeId();
    else this.isLoaded = true;
  }

  getAllByEmployeeId() {
    this.employeeSocialInsuranceInfo = null;
    this.employeeSocialInsuranceService
      .getAllByEmployeeId(this.employeeId)
      .subscribe((res: IResponse<IEmpSocialInsuranceModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.employeeSocialInsuranceInfo = res.data[0];
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialog
      .open(EmpSocialInsuranceDialogComponent, {
        data: {
          employeeId: this.employeeId,
        },
        position: {
          top: "40px",
        },
        minWidth: "600px",
        panelClass: "dialog-emp-form",
        autoFocus: false,
      })
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) this.getAllByEmployeeId();
      });
  }
}
