import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IEmployeeContactModel,
  IEmployeeContactRequest,
} from "../../models/employee-personal-info";
import { IResponse } from "app/shared/interfaces/responces";

@Injectable({
  providedIn: "root",
})
export class EmployeeContactService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmployeeContactRequest) {
    return this.post<IResponse<IEmployeeContactModel>>(
      "Personnel/Employees/Create_contact",
      data
    );
  }

  update(data: IEmployeeContactRequest) {
    return this.post<IResponse<IEmployeeContactModel>>(
      "Personnel/Employees/Edit_contact",
      data
    );
  }

  getAllByEmployeeId(employeeId: number) {
    return this.get<IResponse<IEmployeeContactModel[]>>(
      "Personnel/Employees/GetAll_contact",
      {
        params: {
          $filter: `id eq ${employeeId}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Personnel/Employees/Delete_contact", {
      params: { Id: Id },
    });
  }
}
