import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import { IMilitryGradeModel, IMilitryStatusModel } from "app/hr-setup/models/militry";
import { MilitryService } from "app/hr-setup/services/militry.service";
import {
  IEmployeeMilitaryModel,
  IEmployeeMilitaryRequest,
} from "app/personnel-setup/models/employee-personal-info";
import { EmployeeMilitaryDataService } from "app/personnel-setup/services/employees/employee-military-data.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { CustomValidators } from "ngx-custom-validators";

@Component({
	selector: "app-employee-military-data-dialog",
	templateUrl: "./emp-military-data-dialog.component.html",
	styleUrls: ["./emp-military-data-dialog.component.scss"],
})
export class EmployeeMilitaryDataDialogComponent extends AppHelpers implements OnInit {
	employeeId: number = 0;
	updated: boolean = false;

	mandatoryColomns: string[] = [
		"Ser",
		// "id",
		"status",
		"grade",
		"startDate",
		"endDate",
		"actions",
	];
	optionalColomns: string[] = [];
	dataSource: MatTableDataSource<IEmployeeMilitaryModel>;

	isLoaded: boolean = false;

	form: FormGroup;
	submitted: boolean = false;

	militryStatusList: IMilitryStatusModel[];
	militryGradeList: IMilitryGradeModel[];
	selectedStatus: IMilitryStatusModel;

	constructor(
		private employeeMilitaryDataService: EmployeeMilitaryDataService,
		private militryService: MilitryService,
		@Inject(MAT_DIALOG_DATA) public data: { employeeId: number },
		public dialogRef: MatDialogRef<EmployeeMilitaryDataDialogComponent>
	) {
		super();
		this.employeeId = data.employeeId;
	}

	ngOnInit(): void {
		this.form = this.fb.group(
			{
				id: [0, { nonNullable: true }],
				militaryID: [null, [Validators.required]],
				gradeId: [null],
				startDate: [null, [CustomValidators.date]],
				endDate: [null, [CustomValidators.date]],
				date: [null, [CustomValidators.date]],
			},
			{
				validator: CustomValidator.compareDates("startDate", "endDate"),
			}
		);

		this.dialogRef.backdropClick().subscribe((ele) => {
			this.onCloseDialog();
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	onCloseDialog() {
		this.dialogRef.close(this.updated);
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.checkNoBusinessOrCompany()) {
			this.getAllMilitryStatusList();
			this.getAllMilitryGradeList();
			if (this.employeeId > 0) this.getAll();
		}
	}

	getAll() {
		this.employeeMilitaryDataService
			.getAllByEmployeeId(this.employeeId)
			.subscribe((res: IResponse<IEmployeeMilitaryModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.dataSource = new MatTableDataSource(res.data);
					this.isLoaded = true;
				} else this.errorList(res.message.messages);
			});
	}

	submit() {
		// console.log(this.form.value);
		this.submitted = true;
		if (this.form.valid) {
			const model: IEmployeeMilitaryRequest = {
				Person_ID: this.employeeId,
				id: this.form.value.id,
				militaryID: this.form.value.militaryID,
				gradeId: this.form.value.gradeId,
			};
			if (this.showTwoDates) {
				model.startDate = this.form.value.startDate;
				model.endDate = this.form.value.endDate;
			} else model.date = this.form.value.date;

			model.id == 0 ? this.create(model) : this.update(model);
		} // else this.toastr.error("General.formNotValid");
	}

	create(model: IEmployeeMilitaryRequest) {
		this.showLoading();
		this.employeeMilitaryDataService
			.create(model)
			.subscribe((res: IResponse<IEmployeeMilitaryModel>) => {
				if (res?.message?.messageType == MessageTypes.Success) {
					this.clearForm();
					this.addSuccessfullyMsg(res?.message?.messageTypeName);
					this.getAll();
					this.updated = true;
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	update(model: IEmployeeMilitaryRequest) {
		this.showLoading();
		this.employeeMilitaryDataService
			.update(model)
			.subscribe((res: IResponse<IEmployeeMilitaryModel>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.clearForm();
					this.updateSuccessfullyMsg(res?.message?.messageTypeName);
					this.getAll();
					this.updated = true;
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	employeeMilitaryToEdit(employeeMilitaryInfo: IEmployeeMilitaryModel) {
		this.form.patchValue(employeeMilitaryInfo);
		// console.log(this.form.value);
	}

	onDelete(employeeMilitaryInfo: IEmployeeMilitaryModel): void {
		let msg = this.msgTranslate(employeeMilitaryInfo.status);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(employeeMilitaryInfo);
		});
	}

	delete(employeeMilitaryInfo: IEmployeeMilitaryModel) {
		this.showLoading();
		this.employeeMilitaryDataService.Delete(employeeMilitaryInfo.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				this.getAll();
				if (employeeMilitaryInfo.id == this.formCtrls.id.value) this.clearForm();
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	clearForm() {
		this.form.reset({ id: 0 });
		this.submitted = false;
	}

	getAllMilitryStatusList() {
		this.militryService.getAllMilitryStatus().subscribe((res: IResponse<IMilitryStatusModel[]>) => {
			if ((res.message.messageType = MessageTypes.Success)) {
				this.militryStatusList = res.data;
			} else this.errorList(res.message.messages);
		});
	}

	getAllMilitryGradeList() {
		this.militryService.getAllMilitryGrades().subscribe((res: IResponse<IMilitryGradeModel[]>) => {
			if ((res.message.messageType = MessageTypes.Success)) {
				this.militryGradeList = res.data;
			} else this.errorList(res.message.messages);
		});
	}

	onChangeStatus(values: IMilitryStatusModel[]) {
		this.selectedStatus = values?.length > 0 ? values[0] : null;
		if (this.selectedStatus?.showGrade) this.formCtrls.gradeId.setValidators([Validators.required]);
		else this.formCtrls.gradeId.clearValidators();
		this.formCtrls.gradeId.updateValueAndValidity();
	}

	get showTwoDates() {
		// check if selected status has two dates or one
		return true;
	}
}
