import { Injectable } from "@angular/core";
import { Idropdown } from "app/shared/interfaces/dropdown.interface";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class ContactService extends AppClientService {
  constructor() {
    super();
  }

  getAllTypes() {
    return this.get<IResponse<Idropdown[]>>("HRSetup/contactType/GetAll");
  }

  getAllCategoriesByTypeId(typeId: number) {
    return this.get<IResponse<Idropdown[]>>(
      "HRSetup/contactCategories/GetAll",
      {
        params: {
          $filter: `type_ID eq ${typeId}`,
        },
      }
    );
  }
}
