import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { IEmployeeModel } from "app/personnel-setup/models/employees";
import { EmployeesService } from "app/personnel-setup/services/employees/employees.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmployeeGeneralInfoDialogComponent } from "../emp-general-info-dialog/emp-general-info-dialog.component";
import { ViewsManager } from "app/shared/resources/views-manager";
import { IGUIModel } from "app/shared/interfaces/user-views";

@Component({
	selector: "employee-general-info-card",
	templateUrl: "./emp-general-info-card.component.html",
	styleUrls: ["./emp-general-info-card.component.scss"],
})
export class EmployeeGeneralInfoCardComponent extends AppHelpers implements OnInit {
	@Input() employeeId: number;
	@Output() changeEmployeeId = new EventEmitter<number>();
	employeeInfo: IEmployeeModel;
	isLoaded: boolean = false;
	isCanMadeAction: boolean = false;
	allowedSections: IGUIModel[];

	constructor(private employeesService: EmployeesService, private dialog: MatDialog) {
		super();
	}

	ngOnInit(): void {
		// this.authService.BusinessList$.subscribe((val) => {
		// 	this.businessAndCompanyList = val;
		// });
	}

	// ngOnChanges(changes: SimpleChanges): void {
	//   if (
	//     changes.employeeId.currentValue > 0 &&
	//     this.businessId > 0 &&
	//     this.companyId > 0
	//   )
	//     this.getEmployeeById();
	// }

	override onChangeBusinessAndCompanyId(): void {
		this.employeeInfo = null;
		if (this.employeeId > 0 && this.checkNoBusinessOrCompany()) this.getEmployeeById();
		else this.isLoaded = true;
	}

	override onModulesChanged() {
		this.allowedSections = this.getCustomViewChildren(
			this.getParentViewName,
			ViewsManager.emp_general_info_tab
		);
	}

	getEmployeeById() {
		this.employeeInfo = null;
		this.employeesService.getById(this.employeeId).subscribe((res: IResponse<IEmployeeModel[]>) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.employeeInfo = res.data[0];
				this.isLoaded = true;
			} else this.errorList(res.message.messages);
		});
	}

	openAddEditDialog(): void {
		if (!this.businessCompanyParams.CompanyID) {
			this.warning("chooseCompanyId");
			return;
		}

		let dialogRef = this.dialog.open(EmployeeGeneralInfoDialogComponent, {
			data: {
				employee: this.employeeInfo,
			},
			position: {
				top: "40px",
			},
			minWidth: "600px",
			panelClass: "dialog-emp-form",
			autoFocus: false,
		});
		// after add employee
		dialogRef.componentInstance.afterAdd.subscribe((employeeInfo: IEmployeeModel) => {
			this.changeEmployeeId.emit(employeeInfo.id);
			let url = `/personnel-setup/${this.moduleId}/employees/edit/${employeeInfo.id}`;
			this.navigateTo(url);
			this.employeeInfo = employeeInfo;
		});
		// after close dialog
		dialogRef.afterClosed().subscribe((updated: boolean) => {
			if (updated) this.getEmployeeById();
		});
	}
}
