import { Injectable } from "@angular/core";
import {
  IEmployeeS4HanaModel,
  IEmployeeS4HanaRequest,
} from "app/personnel-setup/models/employee-sap-integration";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeS4HanaService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmployeeS4HanaRequest) {
    return this.post<IResponse<IEmployeeS4HanaModel>>(
      "Personnel/Employees/Create_address",
      data
    );
  }

  update(data: IEmployeeS4HanaRequest) {
    return this.post<IResponse<IEmployeeS4HanaModel>>(
      "Personnel/Employees/Edit_address",
      data
    );
  }

  getAllByEmployeeId(employeeId: number) {
    return this.get<IResponse<IEmployeeS4HanaModel[]>>(
      "Personnel/Employees/GetAll_address",
      {
        params: {
          $filter: `id eq ${employeeId}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Personnel/Employees/Delete_address", {
      params: { Id: Id },
    });
  }
}
