import { Injectable } from "@angular/core";
import {
  IEmpContractDetailsModel,
  IEmpContractDetailsRequest,
} from "app/personnel-setup/models/employee-employment-info";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeContractDetailsService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmpContractDetailsRequest) {
    return this.post<IResponse<IEmpContractDetailsModel>>(
      "Personnel/Employees/Create_ContractDetails",
      data
    );
  }

  update(data: IEmpContractDetailsRequest) {
    return this.post<IResponse<IEmpContractDetailsModel>>(
      "Personnel/Employees/Edit_ContractDetails",
      data
    );
  }

  getAllByEmployeeId(employeeId: number) {
    return this.get<IResponse<IEmpContractDetailsModel[]>>(
      "Personnel/Employees/GetAll_ContractDetails",
      {
        params: {
          $filter: `id eq ${employeeId}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>(
      "Personnel/Employees/Delete_ContractDetails",
      {
        params: { Id: Id },
      }
    );
  }
}
