import { Component, Input, OnInit } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmpS4HanaDialogComponent } from "../emp-s4-hana-dialog/emp-s4-hana-dialog.component";
import { EmployeeS4HanaService } from "app/personnel-setup/services/employees/employee-s4-hana.service";
import { IEmployeeS4HanaModel } from "app/personnel-setup/models/employee-sap-integration";

@Component({
  selector: "app-emp-s4-hana-card",
  templateUrl: "./emp-s4-hana-card.component.html",
  styleUrls: ["./emp-s4-hana-card.component.scss"],
})
export class EmpS4HanaCardComponent extends AppHelpers implements OnInit {
  @Input() employeeId: number;
  employeeS4HanaInfo: IEmployeeS4HanaModel;

  isLoaded: boolean = false;

  constructor(
    private employeeS4HanaService: EmployeeS4HanaService,
    private dialog: MatDialog
  ) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.employeeS4HanaInfo = null;
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany())
      this.getAllByEmployeeId();
    else this.isLoaded = true;
  }

  getAllByEmployeeId() {
    this.employeeS4HanaInfo = null;
    this.employeeS4HanaService
      .getAllByEmployeeId(this.employeeId)
      .subscribe((res: IResponse<IEmployeeS4HanaModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.employeeS4HanaInfo = res.data[0];
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialog
      .open(EmpS4HanaDialogComponent, {
        data: {
          employeeId: this.employeeId,
        },
        position: {
          top: "40px",
        },
        minWidth: "600px",
        panelClass: "dialog-emp-form",
        autoFocus: false,
      })
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) this.getAllByEmployeeId();
      });
  }
}
