import { Component, Input, OnInit } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { IEmployeePayrollModel } from "app/personnel-setup/models/payments-information";
import { EmpPayrollDataDialogComponent } from "../emp-payroll-data-dialog/emp-payroll-data-dialog.component";
import { EmployeePayrollService } from "app/personnel-setup/services/employees/employee-payroll.service";

@Component({
  selector: "app-emp-payroll-data-card",
  templateUrl: "./emp-payroll-data-card.component.html",
  styleUrls: ["./emp-payroll-data-card.component.scss"],
})
export class EmpPayrollDataCardComponent extends AppHelpers implements OnInit {
  @Input() employeeId: number;
  employeePayrollInfo: IEmployeePayrollModel;

  isLoaded: boolean = false;

  constructor(
    private employeePayrollService: EmployeePayrollService,
    private dialog: MatDialog
  ) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.employeePayrollInfo = null;
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany())
      this.getAllByEmployeeId();
    else this.isLoaded = true;
  }

  getAllByEmployeeId() {
    this.employeePayrollInfo = null;
    this.employeePayrollService
      .getAllByEmployeeId(this.employeeId)
      .subscribe((res: IResponse<IEmployeePayrollModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.employeePayrollInfo = res.data[0];
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialog
      .open(EmpPayrollDataDialogComponent, {
        data: {
          employeeId: this.employeeId,
        },
        position: {
          top: "40px",
        },
        minWidth: "600px",
        panelClass: "dialog-emp-form",
        autoFocus: false,
      })
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) this.getAllByEmployeeId();
      });
  }
}
