import { Component, Input, OnInit } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { IEmployeeSapB1Model } from "app/personnel-setup/models/employee-sap-integration";
import { EmpSapB1DialogComponent } from "../emp-sap-b1-dialog/emp-sap-b1-dialog.component";
import { EmployeeSapB1Service } from "app/personnel-setup/services/employees/employee-sap-b1.service";

@Component({
  selector: "app-emp-sap-b1-card",
  templateUrl: "./emp-sap-b1-card.component.html",
  styleUrls: ["./emp-sap-b1-card.component.scss"],
})
export class EmpSapB1CardComponent extends AppHelpers implements OnInit {
  @Input() employeeId: number;
  employeeSapB1Info: IEmployeeSapB1Model;

  isLoaded: boolean = false;

  constructor(
    private employeeSapB1Service: EmployeeSapB1Service,
    private dialog: MatDialog
  ) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.employeeSapB1Info = null;
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany())
      this.getAllByEmployeeId();
    else this.isLoaded = true;
  }

  getAllByEmployeeId() {
    this.employeeSapB1Info = null;
    this.employeeSapB1Service
      .getAllByEmployeeId(this.employeeId)
      .subscribe((res: IResponse<IEmployeeSapB1Model[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.employeeSapB1Info = res.data[0];
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialog
      .open(EmpSapB1DialogComponent, {
        data: {
          employeeId: this.employeeId,
        },
        position: {
          top: "40px",
        },
        minWidth: "600px",
        panelClass: "dialog-emp-form",
        autoFocus: false,
      })
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) this.getAllByEmployeeId();
      });
  }
}
