import { Injectable } from "@angular/core";
import {
  IEmployeeEducationModel,
  IEmployeeEducationRequest,
} from "app/personnel-setup/models/employee-career";
import { Idropdown } from "app/shared/interfaces/dropdown.interface";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeEducationService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmployeeEducationRequest) {
    return this.post<IResponse<IEmployeeEducationModel>>(
      "Personnel/Employees/Create_Education",
      data
    );
  }

  update(data: IEmployeeEducationRequest) {
    return this.post<IResponse<IEmployeeEducationModel>>(
      "Personnel/Employees/Edit_Education",
      data
    );
  }

  getAllByEmployeeId(employeeId: number) {
    return this.get<IResponse<IEmployeeEducationModel[]>>(
      "Personnel/Employees/GetAll_Education",
      {
        params: {
          $filter: `id eq ${employeeId}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Personnel/Employees/Delete_Education", {
      params: { Id: Id },
    });
  }

  getAllMajors() {
    return this.get<IResponse<Idropdown[]>>("Personnel/Employees/major");
  }

  getAllDegrees() {
    return this.get<IResponse<Idropdown[]>>("Personnel/Employees/degree");
  }

  getAllGrades() {
    return this.get<IResponse<Idropdown[]>>("Personnel/Employees/grade");
  }
}
