import { Component, OnInit, Inject } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import { IPermissionTypeModel } from "app/attendance-setup/models/permissions";
import { PermissionTypeService } from "app/attendance-setup/services/permissions.service";
import {
  IEmpPermissionModel,
  IEmpPermissionRequest,
} from "app/personnel-setup/models/employee-balances";
import { EmployeePermissionService } from "app/personnel-setup/services/employees/employee-permission.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
  selector: "emp-permission-dialog",
  templateUrl: "./emp-permission-dialog.component.html",
  styleUrls: ["./emp-permission-dialog.component.scss"],
})
export class EmpPermissionDialogComponent extends AppHelpers implements OnInit {
  employeeId: number = 0;
  updated: boolean = false;

  mandatoryColomns: string[] = [
    "Ser",
    // "id",
    "permission",
    "startDate",
    "endDate",
    "balance",
    "used",
    "remaining",
    "actions",
  ];
  optionalColomns: string[] = [
    "permissionTaken",
    "newHireBalance",
    "newHireCloseDateBalance",
  ];
  dataSource: MatTableDataSource<IEmpPermissionModel>;

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;

  permissionTypeList: IPermissionTypeModel[];

  constructor(
    private employeePermissionService: EmployeePermissionService,
    private permissionTypeService: PermissionTypeService,
    @Inject(MAT_DIALOG_DATA) public data: { employeeId: number },
    public dialogRef: MatDialogRef<EmpPermissionDialogComponent>
  ) {
    super();
    this.employeeId = data.employeeId;
  }

  ngOnInit(): void {
    this.form = this.fb.group(
      {
        id: [0, { nonNullable: true }],
        permissionId: [null, [Validators.required]],
        startDate: [null, [Validators.required]],
        endDate: [null],
        balance: [null, [Validators.required]],
        used: [null, [Validators.required]],
        remaining: [null],
        permissionTaken: [null],
        newHireBalance: [null],
        newHireCloseDateBalance: [null],
      },
      {
        validator: CustomValidator.compareDates("startDate", "endDate"),
      }
    );

    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  onCloseDialog() {
    this.dialogRef.close(this.updated);
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      this.getAllPermissionTypes();
      if (this.employeeId > 0) this.getAll();
    }
  }

  getAll() {
    this.employeePermissionService
      .getAllByEmployeeId(this.employeeId)
      .subscribe((res: IResponse<IEmpPermissionModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmpPermissionRequest = {
        Person_ID: this.employeeId,
        id: this.form.value.id,
        permissionId: this.form.value.permissionId,
        startDate: this.form.value.startDate,
        endDate: this.form.value.endDate,
        balance: this.form.value.balance,
        used: this.form.value.used,
        permissionTaken: this.form.value.permissionTaken,
        newHireBalance: this.form.value.newHireBalance,
        newHireCloseDateBalance: this.form.value.newHireCloseDateBalance,
      };

      model.id == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmpPermissionRequest) {
    this.showLoading();
    this.employeePermissionService
      .create(model)
      .subscribe((res: IResponse<IEmpPermissionModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IEmpPermissionRequest) {
    this.showLoading();
    this.employeePermissionService
      .update(model)
      .subscribe((res: IResponse<IEmpPermissionModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.updated = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  employeePermissionToEdit(employeePermissionInfo: IEmpPermissionModel) {
    this.form.patchValue(employeePermissionInfo);
    // console.log(this.form.value);
  }

  onDelete(employeePermissionInfo: IEmpPermissionModel): void {
    let msg = this.msgTranslate(employeePermissionInfo.permission);
    this.deleateDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(employeePermissionInfo);
    });
  }

  delete(employeePermissionInfo: IEmpPermissionModel) {
    this.showLoading();
    this.employeePermissionService
      .Delete(employeePermissionInfo.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          if (employeePermissionInfo.id == this.formCtrls.id.value)
            this.clearForm();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }

  getAllPermissionTypes() {
    this.permissionTypeService
      .getAll()
      .subscribe((res: IResponse<IPermissionTypeModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.permissionTypeList = res.data;
        } else this.errorList(res.message.messages);
      });
  }
}
