import { Injectable } from "@angular/core";
import {
  IEmployeeDependantModel,
  IEmployeeDependantRequest,
} from "app/personnel-setup/models/employee-personal-info";
import { Idropdown } from "app/shared/interfaces/dropdown.interface";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeDependantService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmployeeDependantRequest) {
    return this.post<IResponse<IEmployeeDependantModel>>(
      "Personnel/Employees/Create_dependant",
      data
    );
  }

  update(data: IEmployeeDependantRequest) {
    return this.post<IResponse<IEmployeeDependantModel>>(
      "Personnel/Employees/Edit_dependant",
      data
    );
  }

  getAllByEmployeeId(employeeId: number) {
    return this.get<IResponse<IEmployeeDependantModel[]>>(
      "Personnel/Employees/GetAll_dependant",
      {
        params: {
          $filter: `id eq ${employeeId}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Personnel/Employees/Delete_dependant", {
      params: { Id: Id },
    });
  }

  getAllRelationships() {
    return this.get<IResponse<Idropdown[]>>(
      "Personnel/Employees/GetAll_Relationship"
    );
  }
}
