import { Component, OnInit, Inject } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import {
	IEmployeeWorkExperienceModel,
	IEmployeeWorkExperienceRequest,
} from "app/personnel-setup/models/employee-career";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { CustomValidators } from "ngx-custom-validators";
import { EmployeeWorkExperienceService } from "app/personnel-setup/services/employees/employee-work-experience.service";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
	selector: "app-employee-work-experience-dialog",
	templateUrl: "./employee-work-experience-dialog.component.html",
	styleUrls: ["./employee-work-experience-dialog.component.scss"],
})
export class EmployeeWorkExperienceDialogComponent extends AppHelpers implements OnInit {
	employeeId: number = 0;
	updated: boolean = false;

	mandatoryColomns: string[] = [
		"Ser",
		// "id",
		"companyName",
		"department",
		"position",
		"startDate",
		"endDate",
		"actions",
	];
	optionalColomns: string[] = [];
	dataSource: MatTableDataSource<IEmployeeWorkExperienceModel>;

	isLoaded: boolean = false;

	form: FormGroup;
	submitted: boolean = false;

	constructor(
		private employeeWorkExperienceService: EmployeeWorkExperienceService,
		@Inject(MAT_DIALOG_DATA) public data: { employeeId: number },
		public dialogRef: MatDialogRef<EmployeeWorkExperienceDialogComponent>
	) {
		super();
		this.employeeId = data.employeeId;
	}

	ngOnInit(): void {
		this.form = this.fb.group(
			{
				id: [0, { nonNullable: true }],
				companyName: [null, [Validators.required, CustomValidator.whiteSpace]],
				department: [null],
				position: [null],
				startDate: [null, [Validators.required, CustomValidators.date]],
				endDate: [null, [Validators.required, CustomValidators.date]],
			},
			{
				validator: CustomValidator.compareDates("startDate", "endDate"),
			}
		);

		this.dialogRef.backdropClick().subscribe((ele) => {
			this.onCloseDialog();
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	onCloseDialog() {
		this.dialogRef.close(this.updated);
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.checkNoBusinessOrCompany()) {
			if (this.employeeId > 0) this.getAll();
		}
	}

	getAll() {
		this.employeeWorkExperienceService
			.getAllByEmployeeId(this.employeeId)
			.subscribe((res: IResponse<IEmployeeWorkExperienceModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.dataSource = new MatTableDataSource(res.data);
					this.isLoaded = true;
				} else this.errorList(res.message.messages);
			});
	}

	submit() {
		// console.log(this.form.value);
		this.submitted = true;
		if (this.form.valid) {
			const model: IEmployeeWorkExperienceRequest = {
				Person_ID: this.employeeId,
				id: this.form.value.id,
				companyName: this.form.value.companyName,
				department: this.form.value.department,
				position: this.form.value.position,
				startDate: this.form.value.startDate,
				endData: this.form.value.endData,
			};

			model.id == 0 ? this.create(model) : this.update(model);
		} // else this.toastr.error("General.formNotValid");
	}

	create(model: IEmployeeWorkExperienceRequest) {
		this.showLoading();
		this.employeeWorkExperienceService
			.create(model)
			.subscribe((res: IResponse<IEmployeeWorkExperienceModel>) => {
				if (res?.message?.messageType == MessageTypes.Success) {
					this.clearForm();
					this.addSuccessfullyMsg(res?.message?.messageTypeName);
					this.getAll();
					this.updated = true;
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	update(model: IEmployeeWorkExperienceRequest) {
		this.showLoading();
		this.employeeWorkExperienceService
			.update(model)
			.subscribe((res: IResponse<IEmployeeWorkExperienceModel>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.clearForm();
					this.updateSuccessfullyMsg(res?.message?.messageTypeName);
					this.getAll();
					this.updated = true;
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	employeeWorkExperienceToEdit(employeeWorkExperienceInfo: IEmployeeWorkExperienceModel) {
		this.form.patchValue(employeeWorkExperienceInfo);
		// console.log(this.form.value);
	}

	onDelete(employeeWorkExperienceInfo: IEmployeeWorkExperienceModel): void {
		let msg = this.msgTranslate(employeeWorkExperienceInfo.companyName);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(employeeWorkExperienceInfo);
		});
	}

	delete(employeeWorkExperienceInfo: IEmployeeWorkExperienceModel) {
		this.showLoading();
		this.employeeWorkExperienceService
			.Delete(employeeWorkExperienceInfo.id)
			.subscribe((res: IResponse) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
					this.getAll();
					if (employeeWorkExperienceInfo.id == this.formCtrls.id.value) this.clearForm();
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	clearForm() {
		this.form.reset({ id: 0 });
		this.submitted = false;
	}
}
