import { Injectable } from "@angular/core";
import {
  IEmployeeDocumentModel,
  IEmployeeDocumentRequest,
} from "app/personnel-setup/models/employee-personal-info";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeDocumentsService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmployeeDocumentRequest) {
    return this.post<IResponse<IEmployeeDocumentModel>>(
      "Personnel/Employees/Create_document",
      data
    );
  }

  update(data: IEmployeeDocumentRequest) {
    return this.post<IResponse<IEmployeeDocumentModel>>(
      "Personnel/Employees/Edit_document",
      data
    );
  }

  getAllByEmployeeId(employeeId: number) {
    return this.get<IResponse<IEmployeeDocumentModel[]>>(
      "Personnel/Employees/GetAll_document",
      {
        params: {
          $filter: `id eq ${employeeId}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Personnel/Employees/Delete_document", {
      params: { Id: Id },
    });
  }
}
